import { connect } from 'react-redux'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { reduxForm, hasSubmitSucceeded, change, formValueSelector } from 'redux-form'
import { withRouter } from 'react-router-dom'
import { getLanguagesByCandidateId } from 'Shared/selectors/languages'
import { getCandidateCvFiles, getAllCandidateFiles } from 'Shared/selectors/files'
import { deleteCandidateLanguage } from 'Shared/ducks/languages'
import {
  uploadFileToS3,
  generateS3Signature,
  deleteCandidateFileById,
  fetchCandidateAttachedCvs
} from 'Shared/ducks/files'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import { fetchCities, fetchCountriesIfNeeded, fetchLanguagesIfNeeded, fetchEducationResources } from 'Shared/ducks/resources'
import validate from './CompleteProfileAboutFormValidator'
import CompleteProfileAboutForm from 'Candidate/forms/CompleteProfile/About/CompleteProfileAboutForm'
import { formName as AddCandidateLanguageDetailsFormName } from 'Candidate/connectors/Forms/Profile/Additional/AddLanguageFormConnector'
import { UPLOAD_DOCUMENT_TYPES } from 'Shared/constants/Files'
import { scrollToFirstError } from 'Shared/helpers/scroll'
import { cleanTrailingSpacesFromHtml } from 'Shared/helpers/string'
import { transformToArray } from 'Shared/helpers/redux'
import { getUserCities } from 'Shared/selectors/cities'
import visaStatuses from 'Shared/constants/VisaStatuses'
import { isFieldInvalid, completeProfileErrors } from 'Shared/selectors/reduxForm'
import { uploadUserAvatar, removeUserAvatar } from 'Shared/ducks/user'
import { updateUserAttributes } from 'Chat/ducks/classes/users'
import { aboutMeAutoDescription } from 'Shared/selectors/autoSuggestedAboutMe'
import { hasPitch } from 'Shared/selectors/user'

const formUtilsService = new FormUtilsService()

export const formName = 'CompleteProfile-AboutForm'

let Form = reduxForm({
  form: formName,
  onSubmitFail: (errors, dispatch, submitError, props) => {
    return scrollToFirstError(props.completeProfileErrors)
  },
  validate,
  enableReinitialize: true,
  onSubmit: (values, dispatch) => Promise.all([
    // FIXME: simple solution that works, but there is someting deeper here, as onChage for CompleteProfile-AdditionalForm after unmount causing it to initialaze whole form again.
    dispatch(change(formName, 'pitch', cleanTrailingSpacesFromHtml(values.pitch))),
    dispatch(updateCandidate({
      ...values,
      pitch: cleanTrailingSpacesFromHtml(values.pitch)
    }))
  ])
})(CompleteProfileAboutForm)

const mapStateToProps = state => {
  const candidateId = state.user.details.id
  const transformedArray = transformToArray(state.resources.countries.entries)
  const {
    pitch,
    dateOfBirth,
    nationality,
    city,
    phoneNumber,
    visaStatus,
    driversLicence,
    countryOfResidence,
    ownsACar,
    handicapDeclaration,
    handicapDetails,
    availableFrom,
    availableTo,
    photoUrl,
    relevantjobOffersNotificationEnabled,
    profileCompletionNotificationEnabled
  } = state.user.details

  return ({
    hasPitch: hasPitch(state),
    impersonation: state.user.impersonation,
    nationalities: formUtilsService.mapCollectionToOptions(transformedArray, 'code', 'nationality'),
    countries: formUtilsService.mapCollectionToOptions(transformedArray, 'code', 'country'),
    cities: formUtilsService.mapArrayToOptions(getUserCities(formName)(state)),
    visaStatuses: formUtilsService.mapCollectionToOptions(visaStatuses, 'code', 'translateKey', true),
    initialValues: {
      pitch: pitch || aboutMeAutoDescription(state),
      nationality,
      city,
      phoneNumber,
      visaStatus,
      countryOfResidence,
      driversLicence,
      ownsACar,
      handicapDeclaration,
      handicapDetails,
      availableFrom,
      availableTo,
      relevantjobOffersNotificationEnabled,
      profileCompletionNotificationEnabled,
      dateOfBirth
    },
    candidateId,
    isFileLoading: state.files.isLoading,
    cvFiles: getCandidateCvFiles(state),
    allFiles: getAllCandidateFiles()(state),
    candidateLanguages: getLanguagesByCandidateId(candidateId)(state),
    languagesList: state.resources.languages.entries,
    hasAddLanguageDetailsSucceeded: hasSubmitSucceeded(AddCandidateLanguageDetailsFormName)(state),
    selectedCountry: formValueSelector(formName)(state, 'countryOfResidence'),
    driversLicence: formValueSelector(formName)(state, 'driversLicence'),
    handicapDeclaration: formValueSelector(formName)(state, 'handicapDeclaration'),
    hasHandicapDeclarationFieldError: isFieldInvalid(formName, 'handicapDeclaration')(state),
    availableFrom: formValueSelector(formName)(state, 'availableFrom'),
    availableTo: formValueSelector(formName)(state, 'availableTo'),
    photoUrl,
    completeProfileErrors: completeProfileErrors(state)
  })
}

const mapActionToProps = dispatch => ({
  fetchCountries: () => dispatch(fetchCountriesIfNeeded()),
  fetchCities: () => dispatch(fetchCities()),
  fetchLanguages: () => dispatch(fetchLanguagesIfNeeded()),
  deleteLanguage: languageId => dispatch(deleteCandidateLanguage(languageId)),
  generateSignature: () => dispatch(generateS3Signature()),
  uploadCvFile: (file, policy, signature) => dispatch(uploadFileToS3(file, policy, signature, UPLOAD_DOCUMENT_TYPES.CV)),
  deleteFile: (fileId, name) => dispatch(deleteCandidateFileById(fileId, name)),
  fetchCandidateAttachedCvs: () => dispatch(fetchCandidateAttachedCvs()),
  changeFieldValue: (fieldName, value) => dispatch(change(formName, fieldName, value)),
  uploadAvatar: async (file, filename) => {
    await dispatch(uploadUserAvatar(file, filename))
    dispatch(updateUserAttributes())
  },
  deleteAvatar: () => dispatch(removeUserAvatar()),
  fetchEducationResources: () => dispatch(fetchEducationResources())
})

export default withRouter(connect(mapStateToProps, mapActionToProps)(Form))



