import { connect } from 'react-redux'
import { deleteEducationDetailsById } from 'Shared/ducks/education'
import { hasSubmitSucceeded, getFormValues } from 'redux-form'
import CompleteProfileEducationForm from 'Candidate/forms/CompleteProfile/Education/CompleteProfileEducationForm'
import { withUniveristesNames } from 'Shared/selectors/education'
import { deleteCertificate } from 'Shared/ducks/certificates'
import { formName as AddUniversityFormName } from 'Candidate/connectors/Forms/Profile/Education/AddEducationDetailsFormConnector'
import { formName as EditUniversityFormName } from 'Candidate/connectors/Forms/Profile/Education/EducationDetailsFormConnector'
import { formName as DefineEducationLevelFormName } from 'Candidate/connectors/Forms/Profile/Education/DefineEducationLevel/DefineEducationLevelFormConnector'
import { formName as AddHighSchoolSucceeded } from 'Candidate/connectors/Forms/Profile/Education/AddHighSchoolEducationDetails/AddHighSchoolEducationDetails'
import { fetchCandidateHighSchoolEducation, deleteHighSchoolEducationDetailsById } from 'Shared/ducks/highSchoolLevelEducation'
import { getEducationHighSchoolDetails } from 'Shared/selectors/highSchoolLevelEducation'
import { getCandidateCertificates } from 'Shared/selectors/certificates'
import * as R from 'ramda'

const mapStateToProps = state => ({
  educationDetails: withUniveristesNames()(state),
  certificates: getCandidateCertificates(state, state.user.details.id),
  hasAddUniversitySucceeded: hasSubmitSucceeded(AddUniversityFormName)(state),
  hasAddHighSchoolSucceeded: hasSubmitSucceeded(AddHighSchoolSucceeded)(state),
  hasEditUniversitySucceeded: hasSubmitSucceeded(EditUniversityFormName)(state),
  selectedEducationLevel: R.propOr('', 'educationLevel', getFormValues(DefineEducationLevelFormName)(state)),
  candidateId: state.user.details.id,
  educationHighSchoolDetails: getEducationHighSchoolDetails(state)
})

const mapDispatchToProps = dispatch => ({
  deleteEducationDetailsById: id => dispatch(deleteEducationDetailsById(id)),
  deleteHighSchoolEducationDetailsById: id => dispatch(deleteHighSchoolEducationDetailsById(id)),
  deleteCertificate: id => dispatch(deleteCertificate(id)),
  fetchCandidateHighSchoolEducation: candidateId => dispatch(fetchCandidateHighSchoolEducation(candidateId))
})

export default connect(mapStateToProps, mapDispatchToProps)(CompleteProfileEducationForm)



