import { reduxForm, getFormValues, isInvalid, getFormSyncErrors } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import ValidationHelperForm from 'Candidate/forms/Profile/ValidationHelperForm'
import { getVolunteeringByCandidateId } from 'Shared/selectors/experience'
import { formName as workExperienceFormName } from 'Candidate/connectors/Forms/Profile/Experience/WorkExperienceFormConnector'
import { scrollToFirstError } from 'Shared/helpers/scroll'

export const formName = 'CompleteProfile-VolunteeringValidationHelperForm'
const fieldName = `validationValue-for-${formName}`

let Form = reduxForm({
  form: formName,
  enableReinitialize: true,
  onSubmit: values => {},
  onSubmitFail: scrollToFirstError,
  validate: values => {
    const errors = {}

    if (values[fieldName] === 'invalid') {
      errors[fieldName] = {
        key: 'errors.atLeastOneActivity'
      }
    }
    return errors
  }
})(ValidationHelperForm)

const mapStateToProps = (state) => {
  const hasExperienceFormValues = getFormValues(workExperienceFormName)(state)
  const volunteering = getVolunteeringByCandidateId(state.user.details.id)(state)

  return ({
    isFormInvalid: isInvalid(formName)(state),
    validationHelperValues: getFormValues(formName)(state),
    validationHelperErrors: getFormSyncErrors(formName)(state),
    initialValues: {
      [fieldName]: !hasExperienceFormValues['hasWorkExperience'] && volunteering.length === 0 ? 'invalid' : 'valid'
    }
  })
}

export default withRouter(connect(mapStateToProps)(Form))



