import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import * as R from 'ramda'
import './CompleteProfileProgressBar.scss'

class CompleteProfileProgressBar extends Component {
  state = {
    width: 0,
    ready: false
  }

  componentDidMount () {
    this.saveWidth()
    window.addEventListener('resize', this.saveWidth)
    setTimeout(() => this.setReady(), 500)
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.props.location.pathname !== prevProps.location.pathname) {
      this.saveWidth()
    }

    if (this.state.ready !== prevState.ready) {
      this.saveWidth()
    }
  }

  setReady = () => this.setState({ ready: true })

  saveWidth = () => this.setState({ width: this.getProgressBarWidth() })

  calculateWidth = stepName => {
    const stepElement = document.getElementById(stepName)
    const stepPosition = () => {
      const stepMetrics = stepElement && stepElement.getBoundingClientRect()

      return R.ifElse(
        R.isNil,
        R.always(0),
        R.propOr(0, 'x')
      )(stepMetrics)
    }

    return ((stepPosition() + 70) * 100) / window.innerWidth
  }
  getStepName = R.pipe(
    R.pathOr('', ['location', 'pathname']),
    R.split('/'),
    R.last
  )

  getProgressBarWidth = () => R.pipe(
    this.getStepName,
    this.calculateWidth
  )(this.props)

  render () {
    return (
      <div className='complete-profile-navigation__progress-bar-wrapper'>
        <div className='complete-profile-navigation__progress-bar' style={{ width: `${this.state.width}%` }} />
      </div>
    )
  }
}

export default withRouter(CompleteProfileProgressBar)



