import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import './CompleteProfileStepHeading.scss'
import { completeProfileSteps } from 'Shared/constants/Profile'
import LazyLoad from 'react-lazyload'
import offsets from 'Shared/constants/LazyLoad'
import * as R from 'ramda'
import withTranslations from 'Shared/hoc/withTranslations'

class CompleteProfileStepHeading extends Component {
  state = {
    step: {
      id: '',
      name: '',
      image: null
    }
  }

  componentDidMount () {
    this.setStepDetails()
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.location.pathname !== this.props.location.pathname) {
      this.setStepDetails()
    }
  }

  getStepName = () => R.pipe(
    R.pathOr('', ['location', 'pathname']),
    R.split('/'),
    R.last
  )(this.props)

  getStepDetails = stepName => R.pipe(
    R.find(
      R.propEq('name', stepName)
    ),
    R.ifElse(
      R.isNil,
      R.always({
        id: '',
        name: '',
        image: null
      }),
      R.identity
    )
  )(completeProfileSteps)

  setStepDetails = () => this.setState({ step: this.getStepDetails(this.getStepName()) })

  render () {
    const { t } = this.props
    const { id, name, image } = this.state.step

    return (
      <div className='complete-profile-step-heading'>
        <div className='complete-profile-step-heading__number'>
          {t('candidate.completeProfile.stepCounter', { value: id, total: completeProfileSteps.length })}
        </div>
        <div className='complete-profile-step-heading__heading'>
          <div className='complete-profile-step-heading__title-wrapper'>
            <h2 className='complete-profile-step-heading__title'>{t(`candidate.completeProfile.${name}.title`)}</h2>
            <p className='complete-profile-step-heading__description'>{t(`candidate.completeProfile.${name}.description`)}</p>
          </div>
          <LazyLoad offset={offsets.small}>
            <img src={image} alt='' className='complete-profile-step-heading__image' />
          </LazyLoad>
        </div>
      </div>
    )
  }
}

CompleteProfileStepHeading.propTypes = {
  step: PropTypes.shape({
    id: PropTypes.number,
    name: PropTypes.string
  }).isRequired,
  heading: PropTypes.string,
  description: PropTypes.string
}

export default withRouter(withTranslations(CompleteProfileStepHeading))



