import { educationLevels } from 'Shared/constants/Candidates'

export default (values, props) => {
  const errors = {}
  switch (true) {
    case values.educationLevel === educationLevels.university && props.educationDetails.length === 0:
      errors['educationLevel'] = {
        key: 'errors.emptyEducationForUniversity'
      }
      break
    case values.educationLevel === educationLevels.highSchool && props.educationHighSchoolDetails.length === 0:
      errors['educationLevel'] = {
        key: 'errors.emptyEducationForHighSchool'
      }
      break
    case values.educationLevel === educationLevels.none:
      errors['educationLevel'] = {
        key: 'errors.requiredField',
        options: {
          field: 'education'
        }
      }
      break
  }

  return errors
}



