import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Collapse, { Panel } from 'rc-collapse'
import { pathOr } from 'ramda'
import { AddHighSchoolEducationDetailsFormConnector, HighSchoolEducationDetailsFormConnector } from 'Candidate/connectors/Forms/Profile/Education/index'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton/index'
import DeleteModal from 'Shared/components/DeleteModal/index'

import withTranslations from 'Shared/hoc/withTranslations/index'

class ProfileEducationHighSchool extends Component {
  state = {
    educationKey: null,
    deleteEducationModalVisible: false,
    educationDetailsRemoveId: '',
    highSchoolFormVisible: false
  }

  componentDidMount () {
    this.props.openAddAsDefault && this.showEducationDetailsForm()
  }

  componentWillReceiveProps ({hasAddHighSchoolSucceeded}) {
    hasAddHighSchoolSucceeded && this.hideEducationDetailsForm()
  }

  showEducationDetailsForm = () => this.setState({ highSchoolFormVisible: true, educationKey: '99' })
  hideEducationDetailsForm = () => this.setState({ highSchoolFormVisible: false })

  setActiveAccordionKey = educationKey => this.setState({ educationKey })
  closeAccordionByKey = () => this.setState({ educationKey: null })

  _openDeleteEducationModal = educationId => () => this.setState({ educationDetailsRemoveId: educationId, deleteEducationModalVisible: true })
  _closeDeleteEducationModal = () => this.setState({ educationDetailsRemoveId: '', deleteEducationModalVisible: false })

  deleteEducationDetails = async () => {
    await this.props.deleteHighSchoolEducationDetailsById(this.state.educationDetailsRemoveId)

    this.closeAccordionByKey()
    return this._closeDeleteEducationModal()
  }

  renderCandidateEducation = () => {
    const { educationHighSchoolDetails } = this.props
    return educationHighSchoolDetails.map((detail, i) => (
      <Panel key={i + 1} header={pathOr(detail.highSchool, ['highSchool', 'name'], detail)}>
        <HighSchoolEducationDetailsFormConnector
          key={detail.id}
          details={detail}
          closeOnSubmit={this.closeAccordionByKey}
          handleDelete={this._openDeleteEducationModal(detail.id)}
        />
      </Panel>
    ))
  }

  render () {
    const { t } = this.props

    return (
      <div>
        <Collapse
          accordion
          className='education-details__collapse'
          activeKey={this.state.educationKey}
          onChange={this.setActiveAccordionKey}
        >
          {this.renderCandidateEducation()}
          {this.state.highSchoolFormVisible && (
            <Panel key={'99'} header={t('profile.education.sections.highSchoolEducationDetails.addNew')}>
              <AddHighSchoolEducationDetailsFormConnector
                closeOnSubmit={this.closeAccordionByKey}
                handleDelete={this.hideEducationDetailsForm}
              />
            </Panel>
          )}
        </Collapse>
        {
          !this.state.highSchoolFormVisible &&
          <AddNewSectionButton
            text={t('profile.education.sections.highSchoolEducationDetails.addNew')}
            onClick={this.showEducationDetailsForm}
          />
        }
        <DeleteModal
          title={t('remove.highSchoolEducationDetails.title')}
          content={t('remove.highSchoolEducationDetails.description')}
          handleDelete={this.deleteEducationDetails}
          isModalVisible={this.state.deleteEducationModalVisible}
          hideDeleteModal={this._closeDeleteEducationModal}
        />
      </div>
    )
  }
}

ProfileEducationHighSchool.propTypes = {
  educationHighSchoolDetails: PropTypes.array.isRequired,
  hasAddHighSchoolSucceeded: PropTypes.bool,
  deleteHighSchoolEducationDetailsById: PropTypes.func.isRequired
}

ProfileEducationHighSchool.defaultProps = {
  openAddAsDefault: false
}

export default withTranslations(ProfileEducationHighSchool)



