import { createSelector } from 'reselect'
import { formValueSelector } from 'redux-form'
import { pipe, flatten } from 'ramda'
import { transformToArray } from 'Shared/helpers/redux'
import { getSelectedCountry } from 'Shared/selectors/countries'

export const getCitiesById = state => state.resources.cities.entries

export const getSelectedCity = formName => state => formValueSelector(formName)(state, 'city')

const mapToOptions = arr => arr.map(e => ({ label: e, value: e }))

const transformCities = pipe(
  transformToArray,
  flatten,
  mapToOptions
)

export const getAllCities = () =>
  createSelector(
    getCitiesById,
    (getCitiesById) => transformCities(getCitiesById)
  )

export const getUserCities = formName =>
  createSelector(
    getSelectedCity(formName),
    getSelectedCountry(formName),
    getCitiesById,
    (userCity, selectedCountry, allCities) => userCity ? [userCity] : allCities[selectedCountry] || []
  )



