import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { profileSteps } from 'Shared/ducks/user/candidate'
import CompleteProfileNavigation from 'Candidate/components/CompleteProfileNavigation'
import { getFormsDirtyState, isAnyFormSubmitting } from 'Shared/selectors/reduxForm'
import { userFirstName } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  formsDirtyState: getFormsDirtyState(state),
  isAnyFormSubmitting: isAnyFormSubmitting(state),
  name: userFirstName(state)
})

const mapDispatchToProps = dispatch => ({
  profileSteps: () => dispatch(profileSteps())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CompleteProfileNavigation))



