import React from 'react'
import CompleteProfileNavigationMobile from './CompleteProfileNavigation.mobile'
import CompleteProfileNavigationWeb from './CompleteProfileNavigation'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CompleteProfileNavigation = props => props.isMobile ? <CompleteProfileNavigationMobile {...props} /> : <CompleteProfileNavigationWeb {...props} />

export default withDeviceWidthDetector(CompleteProfileNavigation)



