import React, { Component } from 'react'
import { completeProfileSteps } from 'Shared/constants/Profile'
import CompleteProfileStepConnector from 'Candidate/connectors/CompleteProfile/CompleteProfileStepConnector'
import { ifElse, isNil, always, pipe, applySpec, values, sum, pathOr, split, last, prop, not, equals, addIndex, map, propEq, find, propOr } from 'ramda'
import './CompleteProfileNavigation.scss'
import NotificationService from 'Shared/services/NotificationService'
import withTranslations from 'Shared/hoc/withTranslations'
import cx from 'classnames'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import { SwipeEntitiesListWrapper, SwipeEntitiesListTab } from 'Shared/components/SwipeEntitiesListTabs'
import { t } from 'i18next'

const mapIndexed = addIndex(map)

class CompleteProfileNavigationMobile extends Component {
  state = {
    stepPositions: {},
    position: 0
  }

  componentDidMount () {
    const { history } = this.props
    this.props.profileSteps()
    this.saveStepPositions()

    const confirmExit = () => {
      const formNotSaved = !NotificationService.bulkSave && this.props.formsDirtyState.length > 0 && !this.props.isAnyFormSubmitting
      if (formNotSaved) {
        return true
      }
    }

    window.onbeforeunload = confirmExit

    this.unblock = history.block(location => {
      const formsNotSaved = !NotificationService.bulkSave && this.props.formsDirtyState.length > 0 && !this.props.isAnyFormSubmitting
      if (formsNotSaved) {
        return this.props.t('common.unsavedChangesDescription')
      }
    })
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.props.location.pathname !== prevProps.location.pathname) {
      this.props.profileSteps()
    }

    if (not(equals(prevState.stepPositions, this.state.stepPositions))) {
      this.changeSwipePosition('', this.getStepName(this.props))
    }
  }

  componentWillUnmount () {
    this.unblock()
    window.onbeforeunload = null
  }

  saveStepPositions = () => {
    let positions = {}

    completeProfileSteps.forEach(e => {
      const stepElement = document.getElementById(e.name)
      const stepPosition = () => {
        const stepMetrics = stepElement && stepElement.getBoundingClientRect()

        return ifElse(
          isNil,
          always(0),
          pipe(
            applySpec({
              offset: propOr(0, 'x'),
              width: propOr(0, 'width')
            }),
            values,
            sum
          )
        )(stepMetrics)
      }
      positions[e.name] = stepPosition()
    })

    return this.setState({ stepPositions: positions })
  }

  getStepName = pipe(
    pathOr('', ['location', 'pathname']),
    split('/'),
    last
  )

  getStepId = () => pipe(
    find(
      propEq('name', this.getStepName(this.props))
    ),
    prop('id')
  )(completeProfileSteps)

  changeSwipePosition = (event, tab) => {
    const delta = this.state.stepPositions[tab] - window.innerWidth

    return this.setState({ position: delta < 0 ? 0 : delta })
  }

  hasStepBottomBorder = (stepId) => {
    const currentId = this.getStepId()
    return currentId >= stepId
  }

  renderStep = (step, index) => {
    const linkClass = cx({
      'complete-profile-step--progress-bar': true,
      'complete-profile-step--progress-bar-visible': this.hasStepBottomBorder(step.id)
    })

    return <SwipeEntitiesListTab
      className={linkClass}
      key={index}
      onClick={this.changeSwipePosition}
      name={step.name}
      text={<CompleteProfileStepConnector id={step.id} name={step.name} />}
    />
  }

  getStepLinks = () => mapIndexed(this.renderStep)(completeProfileSteps)

  redirectToDashboard = () => this.props.history.push('/candidate/dashboard')

  render () {
    return (
      <div className='complete-profile-navigation-mobile'>
        <div onClick={this.redirectToDashboard} className='complete-profile-navigation-mobile__back-btn icon-im-arrow-forward' />
        <PageTopHeaderMobile title={`${t('common.greeting', { name: this.props.name })},`} />
        <div className='complete-profile-navigation-mobile__steps'>
          <SwipeEntitiesListWrapper hideOnScroll={false} position={this.state.position}>
            {this.getStepLinks()}
          </SwipeEntitiesListWrapper>
        </div>
        <div id='confirmPrompt' />
      </div>
    )
  }
}

export default withTranslations(CompleteProfileNavigationMobile)



