import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { hasSubmitSucceeded, formValueSelector } from 'redux-form'
import { deleteExperience } from 'Shared/ducks/experiences'
import CompleteProfileExperienceForm from 'Candidate/forms/CompleteProfile/Experience/CompleteProfileExperienceForm'
import { getExperiencesByCandidateId, getVolunteeringByCandidateId, hasMoreThan6YearsOfExperience } from 'Shared/selectors/experience'
import { formName as AddExperienceDetailsFormName } from 'Candidate/connectors/Forms/Profile/Experience/AddExperienceDetailsFormConnector'
import { formName as AddVolunteeringDetailsFormName } from 'Candidate/connectors/Forms/Profile/Experience/AddVolunteeringDetailsFormConnector'
import { formName as HasWorkExperienceFormName } from 'Candidate/connectors/Forms/Profile/Experience/WorkExperienceFormConnector'
import NotificationService from 'Shared/services/NotificationService'
import UserService from 'Shared/services/UserService'
import { logout } from 'Shared/ducks/user'
import { t } from 'i18next'

const userService = new UserService()

const mapStateToProps = state => {
  const userId = state.user.details.id

  return ({
    userId,
    hasWorkExperience: formValueSelector(HasWorkExperienceFormName)(state, 'hasWorkExperience'),
    experiences: getExperiencesByCandidateId(userId)(state),
    volunteering: getVolunteeringByCandidateId(userId)(state),
    hasAddExperienceDetailsSucceeded: hasSubmitSucceeded(AddExperienceDetailsFormName)(state),
    hasAddVolunteeringDetailsSucceeded: hasSubmitSucceeded(AddVolunteeringDetailsFormName)(state),
    hasMoreThan6YearsOfExperience: hasMoreThan6YearsOfExperience(state, userId)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteExperience: id => dispatch(deleteExperience(id)),
  deleteAccount: async () => {
    try {
      await userService.deleteCandidateAccount()
      dispatch(logout())
      return ownProps.history.push('/candidate/sign-up/cancel')
    } catch (e) {
      return NotificationService.error(t('alerts.deleteAccountFailed'))
    }
  }
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CompleteProfileExperienceForm))



