import React, { Component } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import './CompleteProfileSectionTitle.scss'

class CompleteProfileSectionTitle extends Component {
  render () {
    return (
      <div className='complete-profile-section-title'>
        <h2 className='complete-profile-section-title__heading'>
          {this.props.title} {this.props.optional && <span className='complete-profile-section-title__optional'> - {this.props.t('common.optional')}</span>}
        </h2>
        {this.props.description && <p className='complete-profile-section-title__description'>{this.props.description}</p>}
      </div>
    )
  }
}

export default withTranslations(CompleteProfileSectionTitle)



