import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CompleteProfileStep from 'Candidate/components/CompleteProfileNavigation/components/CompleteProfileStep'
import { isStepCompleted } from 'Shared/selectors/user'

const mapStateToProps = (state, ownProps) => ({
  isStepCompleted: isStepCompleted(state, ownProps.name)
})

export default withRouter(connect(mapStateToProps)(CompleteProfileStep))



