import React, { Component } from 'react'
import { Form, Field } from 'redux-form'
import { Input } from 'Shared/components/FormElements'
import Label from 'Shared/components/FormElements/Label'
import withTranslations from 'Shared/hoc/withTranslations/index'

import './ValidationHelperForm.scss'

class ValidationHelperForm extends Component {
  render () {
    const { t, handleSubmit, isFormInvalid, validationHelperErrors, submitFailed } = this.props
    const fieldName = `validationValue-for-${this.props.form}`

    return (
      <Form onSubmit={handleSubmit}>
        <Field
          component={Input}
          name={fieldName}
          type='hidden'
        />
        { isFormInvalid && submitFailed &&
        <Label className='validation-helper__error'>
          {t(validationHelperErrors[fieldName].key, validationHelperErrors[fieldName].options)}
        </Label> }
      </Form>
    )
  }
}

export default withTranslations(ValidationHelperForm)



