import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import DefineEducationLevel from 'Candidate/components/ProfileEducation/DefineEducationLevel/index'
import validate from './validate'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import {
  isFieldInvalid,
  getFieldErrorMessage,
  completeProfileErrors
} from 'Shared/selectors/reduxForm'
import { scrollToFirstError } from 'Shared/helpers/scroll'
import { getEducationHighSchoolDetails } from 'Shared/selectors/highSchoolLevelEducation'
import { withUniveristesNames } from 'Shared/selectors/education'
import { notEquals } from 'Shared/helpers/Ramda'

export const formName = 'CompleteProfile-DefineEducationLevel'

const Form = reduxForm({
  form: formName,
  validate,
  enableReinitialize: true,
  shouldValidate: ({nextProps, props}) => {
    return notEquals(nextProps)(props)
  },
  onSubmitFail: (errors, dispatch, submitError, props) => {
    return scrollToFirstError(props.completeProfileErrors)
  },
  onSubmit: (values, dispatch) => dispatch(updateCandidate(values))
})(DefineEducationLevel)

const mapStateToProps = state => {
  return ({
    initialValues: {
      educationLevel: state.user.details.educationLevel
    },
    hasEducationLevelError: isFieldInvalid(formName, 'educationLevel')(state),
    errorMessage: getFieldErrorMessage(formName, 'educationLevel')(state),
    educationHighSchoolDetails: getEducationHighSchoolDetails(state),
    educationDetails: withUniveristesNames()(state),
    completeProfileErrors: completeProfileErrors(state)
  })
}

export default connect(mapStateToProps)(Form)



