import React, { Component } from 'react'
import { completeProfileSteps } from 'Shared/constants/Profile'
import { Link } from 'react-router-dom'
import logo from 'Shared/assets/images/brand/oliv-logo.svg'
import CompleteProfileProgressBar from 'Candidate/components/CompleteProfileNavigation/components/CompleteProfileProgressBar'
import LoggedUserConnector from 'Shared/connectors/LoggedUserConnector'
import CompleteProfileStepConnector from 'Candidate/connectors/CompleteProfile/CompleteProfileStepConnector'
import * as R from 'ramda'
import './CompleteProfileNavigation.scss'
import NotificationService from 'Shared/services/NotificationService'
import withTranslations from 'Shared/hoc/withTranslations'

const mapIndexed = R.addIndex(R.map)

class CompleteProfileNavigation extends Component {
  componentDidMount () {
    const { history } = this.props
    this.props.profileSteps()

    const confirmExit = () => {
      const formNotSaved = !NotificationService.bulkSave && this.props.formsDirtyState.length > 0 && !this.props.isAnyFormSubmitting
      if (formNotSaved) {
        return true
      }
    }

    window.onbeforeunload = confirmExit

    this.unblock = history.block(location => {
      const formsNotSaved = !NotificationService.bulkSave && this.props.formsDirtyState.length > 0 && !this.props.isAnyFormSubmitting
      if (formsNotSaved) {
        return this.props.t('common.unsavedChangesDescription')
      }
    })
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.props.location.pathname !== prevProps.location.pathname) {
      this.props.profileSteps()
    }
  }

  componentWillUnmount () {
    this.unblock()
    window.onbeforeunload = null
  }

  renderStep = (step, index) => <CompleteProfileStepConnector name={step.name} id={step.id} key={index} />
  getStepLinks = () => mapIndexed(this.renderStep)(completeProfileSteps)

  render () {
    return (
      <div className='complete-profile-navigation parsing--hidden'>
        <div className='complete-profile-navigation__wrapper'>
          <div className='complete-profile-navigation__section complete-profile-navigation__section--margin'>
            <Link to='/candidate/dashboard'>
              <img src={logo} alt='Oliv' />
            </Link>
          </div>
          <div className='complete-profile-navigation__section'>
            <ol className='complete-profile-navigation__steps'>
              {this.getStepLinks()}
            </ol>
          </div>
          <div className='complete-profile-navigation__section'>
            <LoggedUserConnector />
          </div>
        </div>
        <CompleteProfileProgressBar />
        <div id='confirmPrompt' />
      </div>
    )
  }
}

export default withTranslations(CompleteProfileNavigation)



