import React, { Component } from 'react'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from 'Shared/components/FormElements/Label'
import UploadedFileFormConnector from 'Candidate/connectors/UploadedFileFormConnector/UploadedFileFormConnector'
import UploadedCvFileFormConnector from 'Candidate/connectors/UploadedFileFormConnector/UploadedCvFileFormConnector'
import DeleteModal from 'Shared/components/DeleteModal'
import { UPLOAD_DOCUMENT_TYPES } from 'Shared/constants/Files'
import Modal from 'Shared/components/Modal'
import { Trans } from 'react-i18next'
import FilesUploadProgress from 'Shared/connectors/Components/FilesUploadProgressConnector'
import { ifElse, map, propEq } from 'ramda'
import { StaticRoutes } from 'Shared/constants/Routes'
// TODO: Use this double confirm DELETE when the feature should be available
// import DeleteAttachedCvModal from 'Candidate/components/DeleteAttachedCvModal/DeleteAttachedCvModal'

class UploadedFilesList extends Component {
  static propTypes = {
    files: PropTypes.array.isRequired,
    candidateAttachedCvs: PropTypes.array.isRequired,
    deleteFile: PropTypes.func.isRequired
  }

  state = {
    fileRemoveId: '',
    fileRemoveName: '',
    fileRemoveType: '',
    showDeleteFileModal: false,
    showDeleteAttachedCvModal: false
  }

  refreshRemoveFileDetails = () => this.setState({ fileRemoveId: '', fileRemoveType: '' })

  _openDeleteModal = (fileId, name, type) => () => this.setState({ fileRemoveId: fileId, fileRemoveName: name, showDeleteFileModal: true, fileRemoveType: type })
  _hideDeleteModal = () => this.setState({ showDeleteFileModal: false })

  openDeleteAttachedCvModal = (fileId, name, type) => () => this.setState({ fileRemoveId: fileId, fileRemoveName: name, showDeleteAttachedCvModal: true, fileRemoveType: type })
  hideDeleteAttachedCvModal = () => this.setState({ showDeleteAttachedCvModal: false })

  _deleteFile = () => {
    const { fileRemoveId, fileRemoveName, fileRemoveType } = this.state
    this.props.deleteFile(fileRemoveId, fileRemoveName)
    this.isCvFileAttached(fileRemoveType, fileRemoveId) ? this.hideDeleteAttachedCvModal() : this._hideDeleteModal()
    return this.refreshRemoveFileDetails()
  }

  isCvFileAttached = (fileType, fileId) => fileType === UPLOAD_DOCUMENT_TYPES.CV && this.props.candidateAttachedCvs.indexOf(fileId) >= 0

  additionalFile = file => <UploadedFileFormConnector key={file.id} file={file} onDelete={this._openDeleteModal(file.id, file.name, file.documentType)} />
  cvFile = file => <UploadedCvFileFormConnector key={file.id} file={file} onDelete={this.isCvFileAttached(file.documentType, file.id) ? this.openDeleteAttachedCvModal(file.id, file.name, file.documentType) : this._openDeleteModal(file.id, file.name, file.documentType)} />
  getProperFileType = ifElse(
    propEq('documentType', UPLOAD_DOCUMENT_TYPES.CV),
    this.cvFile,
    this.additionalFile
  )
  renderFiles = map(this.getProperFileType)

  render () {
    const { files, t } = this.props
    const { fileRemoveType, fileRemoveId, fileRemoveName } = this.state

    return (
      <div className='file-input__uploaded-documents'>
        <Label>{t('common.uploadedDocuments')}</Label>
        {this.renderFiles(files)}
        <FilesUploadProgress fileType={this.props.initialFileType} />
        {
          this.isCvFileAttached(fileRemoveType, fileRemoveId)
            ? <Modal
              title={t('modals.cvLinkedToApplicationModal.title')}
              handleSubmit={this.hideDeleteAttachedCvModal}
              handleClose={this.hideDeleteAttachedCvModal}
              show={this.state.showDeleteAttachedCvModal}
              submitText={t('common.okGotIt')}
              showContact={false}
              showCancel={false}
            >
              <Trans i18nKey='modals.cvLinkedToApplicationModal.description'>
                You have attached this CV to a job application on Oliv. As the application has already
                been sent to the employer, you will not be able to delete it. You can still upload a new
                CV to your profile and attach it to future job applications. If you have any questions,
                please <a href={StaticRoutes.contact}>contact</a> us. We'll be happy to help.
              </Trans>
            </Modal>
            // TODO: Use this double confirm DELETE when the feature should be available
            // ? <DeleteAttachedCvModal deleteFile={this._deleteFile} show={this.state.showDeleteAttachedCvModal} handleClose={this.hideDeleteAttachedCvModal} />
            : <DeleteModal
              title={t('remove.file.title', {name: fileRemoveName})}
              content={t('remove.file.description', {name: fileRemoveName})}
              handleDelete={this._deleteFile}
              isModalVisible={this.state.showDeleteFileModal}
              hideDeleteModal={this._hideDeleteModal}
              submitText={this.isCvFileAttached(fileRemoveType, fileRemoveId) && t('remove.cvAttached.title')}
            />
        }
      </div>
    )
  }
}

export default withTranslations(UploadedFilesList)



