import { connect } from 'react-redux'
import { submit } from 'redux-form'
import {
  getFormsDirtyState,
  isAnyFormInvalid,
  getCompleteProfileForms,
  isAnyFormSubmitting,
  isEveryFormSubmitSucceeded,
  isAnyFormSubmitFailed
} from 'Shared/selectors/reduxForm'
import FooterNavigation from 'Shared/components/ProfileNavigation/FooterNavigation'
import { profileSteps } from 'Shared/ducks/user/candidate'

const mapStateToProps = state => ({
  dirtyForms: getFormsDirtyState(state),
  isAnyFormInvalid: isAnyFormInvalid(state),
  completeProfileForms: getCompleteProfileForms(state),
  isAnyFormSubmitting: isAnyFormSubmitting(state),
  isEveryFormSubmitSucceeded: isEveryFormSubmitSucceeded(state),
  isAnyFormSubmitFailed: isAnyFormSubmitFailed(state)
})

const mapDispatchToProps = dispatch => ({
  saveAllDirtyForms: (allForms) => {
    return Promise.all(allForms.map(form => dispatch(submit(form))))
  },
  profileSteps: () => dispatch(profileSteps())
})

export default connect(mapStateToProps, mapDispatchToProps)(FooterNavigation)



