import React, { Component } from 'react'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import NotificationService from 'Shared/services/NotificationService'
import withTranslations from 'Shared/hoc/withTranslations'
import { PrimaryButton, SecondaryButton } from 'Shared/components/Button'
import { completeProfileSteps } from 'Shared/constants/Profile'
import cx from 'classnames'
import { isArabicLayout } from 'Shared/helpers/layout'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import './FooterNavigation.scss'

class FooterNavigation extends Component {
  static defaultProps = {
    saveAndGoNext: () => {}
  }

  state = {
    loading: false
  }

  hasPreviousStep = (currentStep) => currentStep > 1
  isLastStep = currentStep => currentStep === completeProfileSteps.length

  componentWillReceiveProps (nextProps) {
    const { isAnyFormSubmitting, isEveryFormSubmitSucceeded, isAnyFormSubmitFailed } = nextProps
    if (NotificationService.bulkSave && !isAnyFormSubmitting) {
      if (isEveryFormSubmitSucceeded) {
        this.isLastStep(this.props.currentStep) ? this.redirectToDashboard() : this.handleSaveAndGoNext()
        NotificationService.bulkSave = false
        this.isLastStep(this.props.currentStep) && NotificationService.success(this.props.t('alerts.formSuccessfullySaved'))
        return this.stopLoading()
      }

      if (isAnyFormSubmitFailed) {
        NotificationService.error(this.props.t('alerts.invalidForm'))
        NotificationService.bulkSave = false
        return this.stopLoading()
      }
    }
  }

  startLoading = () => this.setState({ loading: true })
  stopLoading = () => this.setState({ loading: false })

  handleSaveAndGoNext = () => {
    this.props.saveAndGoNext()
    this.stopLoading()
  }

  saveCurrentStep = () => {
    NotificationService.bulkSave = true
    if (this.props.dirtyForms.length > 0 || this.props.isAnyFormInvalid) {
      return this.props.saveAllDirtyForms(this.props.completeProfileForms)
    }
    NotificationService.bulkSave = false
    return this.handleSaveAndGoNext()
  }

  saveLastStep = () => {
    NotificationService.bulkSave = true
    if (this.props.dirtyForms.length > 0 || this.props.isAnyFormInvalid) {
      return this.props.saveAllDirtyForms(this.props.completeProfileForms)
    }
    NotificationService.bulkSave = false
    return this.redirectToDashboard()
  }

  handleNextButton = () => {
    this.startLoading()
    return this.isLastStep(this.props.currentStep)
      ? this.saveLastStep()
      : this.saveCurrentStep()
  }

  getPreviousButton (t) {
    const { goPrevious } = this.props
    const iconClass = cx({
      'icon-im-caret-left': !isArabicLayout,
      'icon-im-caret-right': isArabicLayout
    })

    return (
      <SecondaryButton
        disabled={this.state.loading}
        onClick={goPrevious}
        withIcon
        icon
        iconClassName={iconClass}
      >
        {t('common.previousStep')}
      </SecondaryButton>
    )
  }

  getNextButton (t) {
    return (
      <PrimaryButton
        disabled={this.state.loading}
        onClick={this.handleNextButton}
        className='profile__form-footer--save-button'
      >
        {t('common.saveAndContinue')}
      </PrimaryButton>
    )
  }

  redirectToDashboard = () => {
    const { history } = this.props
    this.props.profileSteps()
    return history.push({ pathname: '/jobs' })
  }

  render () {
    const { t, currentStep, isMobile } = this.props
    const previousStepButton = this.hasPreviousStep(currentStep) ? this.getPreviousButton(t) : null
    const nextStepButton = this.getNextButton(t)
    const containerClass = cx({
      'footer-navigation': !isMobile,
      'footer-navigation-mobile': isMobile
    })

    return (
      <div className={containerClass}>
        {previousStepButton}
        {nextStepButton}
      </div>
    )
  }
}

FooterNavigation.propTypes = {
  currentStep: PropTypes.number.isRequired,
  saveAndGoNext: PropTypes.func,
  goPrevious: PropTypes.func,
  saveAllDirtyForms: PropTypes.func.isRequired,
  dirtyForms: PropTypes.array.isRequired,
  isAnyFormSubmitting: PropTypes.bool,
  isEveryFormSubmitSucceeded: PropTypes.bool
}

export default compose(
  withRouter,
  withTranslations,
  withDeviceWidthDetector
)(FooterNavigation)



