import React from 'react'
import CompleteProfileViewMobile from './CompleteProfileView.mobile'
import CompleteProfileViewWeb from './CompleteProfileView'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CompleteProfileView = props => props.isMobile ? <CompleteProfileViewMobile {...props} /> : <CompleteProfileViewWeb {...props} />

export default withDeviceWidthDetector(CompleteProfileView)



