import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Form, Field } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Radio, RadioWrapper } from 'Shared/components/FormElements'
import { educationLevels } from 'Shared/constants/Candidates'

class DefineEducationLevel extends Component {
  render () {
    const { t } = this.props
    return (
      <Form onSubmit={this.props.handleSubmit}>
        <div className='form__group'>
          <RadioWrapper
            legend={t('candidate.completeProfile.education.educationLevelLabel')}
            hasError={this.props.hasEducationLevelError}
            error={this.props.errorMessage}
            name='educationLevel'
          >
            <Field
              component={Radio}
              text={t('common.highSchool')}
              name='educationLevel'
              radioButtonValue={educationLevels.highSchool}
            />
            <Field
              component={Radio}
              text={t('common.university')}
              brackets={t('candidate.completeProfile.education.studentOrGraduate')}
              name='educationLevel'
              radioButtonValue={educationLevels.university}
            />
          </RadioWrapper>
        </div>
      </Form>
    )
  }
}

DefineEducationLevel.propTypes = {
  educationLevel: PropTypes.oneOf(Object.values(educationLevels)),
  hasError: PropTypes.bool,
  errorMessage: PropTypes.object,
  highSchoolEducationLength: PropTypes.number,
  universityEducationLength: PropTypes.number
}

export default withTranslations(DefineEducationLevel)



