import React, { Component } from 'react'
import ProfileEducationUniveristy from 'Candidate/components/ProfileEducation/ProfileEducationUniveristy'
import ProfileEducationHighSchool from 'Candidate/components/ProfileEducation/ProfileEducationHighSchool'
import DefineEducationLevelFormConnector from 'Candidate/connectors/Forms/Profile/Education/DefineEducationLevel/DefineEducationLevelFormConnector'
import CompleteProfileSectionTitle from 'Candidate/components/CompleteProfileSectionTitle'
import { educationLevels } from 'Shared/constants/Candidates'
import withTranslations from 'Shared/hoc/withTranslations'
import './CompleteProfileEducationForm.scss'

class CompleteProfileEducationForm extends Component {
  componentDidMount () {
    this.props.fetchCandidateHighSchoolEducation(this.props.candidateId)
  }

  render () {
    const { t, selectedEducationLevel } = this.props

    return (
      <div className='complete-profile-education'>
        <DefineEducationLevelFormConnector />
        <CompleteProfileSectionTitle title={t('candidate.completeProfile.education.addEducationDetails')} />
        {
          selectedEducationLevel === educationLevels.highSchool &&
          <ProfileEducationHighSchool
            hasAddHighSchoolSucceeded={this.props.hasAddHighSchoolSucceeded}
            educationHighSchoolDetails={this.props.educationHighSchoolDetails}
            deleteHighSchoolEducationDetailsById={this.props.deleteHighSchoolEducationDetailsById}
          />
        }
        {
          selectedEducationLevel === educationLevels.university &&
          <ProfileEducationUniveristy
            hasAddUniversitySucceeded={this.props.hasAddUniversitySucceeded}
            educationDetails={this.props.educationDetails}
            deleteEducationDetailsById={this.props.deleteEducationDetailsById}
          />
        }
      </div>
    )
  }
}

export default withTranslations(CompleteProfileEducationForm)



