import React, { Component, Fragment } from 'react'
import './CompleteProfileAboutForm.scss'
import PropTypes from 'prop-types'
import Collapse, { Panel } from 'rc-collapse'
import { Form, Field } from 'redux-form'
import { lengthLimit } from 'Shared/constants/Editor'
import { MAX_UPLOAD_FILE_SIZE, MAX_CV_FILE_COUNT } from 'Shared/constants/S3'
import withTranslations from 'Shared/hoc/withTranslations'
import {
  Editor,
  S3Uploader,
  Select,
  SearchMap,
  RadioWrapper,
  Radio,
  InputDatePicker,
  SwitchToggle,
  SingleImageUploader, Textarea, PhoneInput
} from 'Shared/components/FormElements'
import LanguageFormConnector from 'Candidate/connectors/Forms/Profile/Additional/LanguageFormConnector'
import AddLanguageFormConnector from 'Candidate/connectors/Forms/Profile/Additional/AddLanguageFormConnector'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import DeleteModal from 'Shared/components/DeleteModal'
import { pathOr } from 'ramda'
import UploadDocumentsValidationHelperFormConnector from 'Candidate/connectors/Forms/Profile/Additional/UploadDocumentsValidationHelperFormConnector.js'
import LanguagesValidationHelperFormConnector from 'Candidate/connectors/Forms/Profile/Additional/LanguagesValidationHelperFormConnector.js'
import UploadedCvFilesListConnector from 'Candidate/connectors/UploadedFilesList/UploadedCvFilesListConnector'
import CompleteProfileSectionTitle from 'Candidate/components/CompleteProfileSectionTitle'
import CompleteProfileDivider from 'Candidate/components/CompleteProfileDivider'
import { normalizeRadio } from 'Shared/helpers/forms'
import { handicapDeclarationType } from 'Shared/constants/Candidates'
import { Trans } from 'react-i18next'
import moment from 'moment'
import { convertDateToMoment } from 'Shared/helpers/date'
import { isNotNil } from 'Shared/helpers/Ramda'
import Notification from 'Shared/components/Notification/index'
import Tooltip from 'Shared/components/Tooltip/Tooltip'

const handicapDeclarationLink = 'https://government.ae/en/information-and-services/social-affairs/special-needs'

class CompleteProfileAboutForm extends Component {
  state = {
    languageRemoveId: '',
    addNewLanguageFormVisible: false,
    deleteLanguageModalVisible: false,
    languageKey: null
  }

  static propTypes = {
    fetchLanguages: PropTypes.func.isRequired,
    fetchCandidateAttachedCvs: PropTypes.func.isRequired
  }

  static defaultProps = {
    candidateLanguages: [],
    languagesList: {}
  }

  async componentDidMount () {
    this.props.fetchCountries()
    this.props.fetchCities()
    this.props.fetchCandidateAttachedCvs()
    this.props.fetchLanguages()
    this.props.fetchEducationResources()
  }

  componentWillReceiveProps (nextProps) {
    nextProps.hasAddLanguageDetailsSucceeded && this._hideAddNewLanguageForm()
  }

  setActiveAccordionKey = key => keyValue => this.setState({ [key]: keyValue })
  closeAccordionByKey = key => () => this.setState({ [key]: null })

  _openDeleteLanguageModal = languageId => () => this.setState({ languageRemoveId: languageId, deleteLanguageModalVisible: true })
  _closeDeleteLanguageModal = () => this.setState({ languageRemoveId: '', deleteLanguageModalVisible: false })

  _showAddNewLanguageForm = () => this.setState({ addNewLanguageFormVisible: true, languageKey: '99' })
  _hideAddNewLanguageForm = () => this.setState({ addNewLanguageFormVisible: false })

  deleteLanguageDetails = accordionKey => async () => {
    const { deleteLanguage } = this.props
    await deleteLanguage(this.state.languageRemoveId)

    this.closeAccordionByKey(accordionKey)()
    this._closeDeleteLanguageModal()
  }

  renderCandidateLanguages = () => {
    const { t, candidateLanguages, languagesList } = this.props

    return candidateLanguages.map((language, i) => {
      const panelName = pathOr(`${t('common.loading')}...`, [language.languageCode.toUpperCase(), 'language'], languagesList)

      return (
        <Panel key={i + 1} header={panelName} >
          <LanguageFormConnector
            key={language.id}
            details={language}
            closeOnSubmit={this.closeAccordionByKey('languageKey')}
            handleDelete={this._openDeleteLanguageModal(language.id)}
          />
        </Panel>
      )
    })
  }

  getHandicapDeclarationDescription = () => (
    <Trans i18nKey={`profile.basics.handicapDeclaration.description`}>
      To help us achieve our objective of empowering <a href={handicapDeclarationLink} target='_blank'>People of Determination</a>, we invite you to
      voluntarily provide demographic information. Inclusion of this information will enable
      employers who are supporters of People of Determination to identify you.
    </Trans>
  )

  uploadTextContent = {
    uploadButtonText: 'common.changePhoto',
    uploaderContent: 'common.uploadProfilePhotoInfo',
    editorModalTitle: 'common.createProfilePhoto',
    deleteModalTitle: 'remove.photo.title',
    deleteModalContent: 'remove.photo.description'
  }

  ageField = () => {
    const { t } = this.props

    return (
      <Fragment>
        <Field
          component={InputDatePicker}
          label={t('forms.dateOfBirth.label')}
          tooltip={<Tooltip content={t('forms.dateOfBirth.tooltipContent')} position={'top'}><div className='complete-profile-about__age-tooltip'>{t('forms.dateOfBirth.tooltipLabel')}</div></Tooltip>}
          name='dateOfBirth'
          icon={'ion-md-clock'}
          floatRight
          minDate={moment().utc().subtract(72, 'years')}
          maxDate={moment().utc().subtract(14, 'years')}
          scrollableYearDropdown
          showYearDropdown
          showMonthDropdown
          yearDropdownItemNumber={40}
          placeholderText={t('forms.dateOfBirth.placeholder')}
          format={value => value ? moment(value, moment.ISO_8601) : null}
        />
        <div className='complete-profile-about__age-info'>{t('authorization.cannotBeChanged')}</div>
      </Fragment>
    )
  }

  phoneInputField = () => {
    const { t } = this.props

    return (
      <Field
        component={PhoneInput}
        className={'form-group'}
        label={t('forms.phoneNumber.label')}
        name='phoneNumber'
      />
    )
  }

  render () {
    const { t, startDate, endDate, handleSubmit, hasHandicapDeclarationFieldError, handicapDeclaration, hasPitch, initialValues: { pitch, dateOfBirth } } = this.props

    return (
      <div className='complete-profile-about'>
        <Form onSubmit={handleSubmit}>

          <div>
            <CompleteProfileSectionTitle
              optional
              title={t('candidate.completeProfile.about.addProfilePhoto')}
              description={t('candidate.completeProfile.about.addProfilePhotoDescription')} />
            <SingleImageUploader
              layout='profile'
              textConfig={this.uploadTextContent}
              uploadFile={this.props.uploadAvatar}
              deleteFile={this.props.deleteAvatar}
              imageUrl={this.props.photoUrl}
            />
          </div>

          <div>
            <CompleteProfileSectionTitle
              title={t('candidate.completeProfile.about.addCv')} />
            <UploadDocumentsValidationHelperFormConnector />
            <Field
              name='uploadCvFile'
              component={S3Uploader}
              loading={this.props.isFileLoading}
              generateSignature={this.props.generateSignature}
              deleteFile={this.props.deleteFile}
              uploadFile={this.props.uploadCvFile}
              maxSize={MAX_UPLOAD_FILE_SIZE}
              maxFileCount={MAX_CV_FILE_COUNT}
              files={this.props.cvFiles}
              allFiles={this.props.allFiles}
              type='file'
              label={t('common.uploadDocuments')}
              placeholder={t('common.uploadDocuments')}
            />
            <UploadedCvFilesListConnector />
          </div>

        </Form>

        <CompleteProfileDivider />

        <div>
          <CompleteProfileSectionTitle
            title={t('candidate.completeProfile.about.addLanguages')} />
          <LanguagesValidationHelperFormConnector />
          <Collapse accordion className='education-details__collapse' activeKey={this.state.languageKey} onChange={this.setActiveAccordionKey('languageKey')}>
            {this.renderCandidateLanguages()}
            {this.state.addNewLanguageFormVisible && (
              <Panel key={'99'} header={t('profile.additional.sections.languages.addNew')}>
                <AddLanguageFormConnector
                  closeOnSubmit={this.closeAccordionByKey('languageKey')}
                  handleDelete={this._hideAddNewLanguageForm}
                />
              </Panel>
            )}
          </Collapse>
          {!this.state.addNewLanguageFormVisible &&
          <AddNewSectionButton text={t('profile.additional.sections.languages.addNew')} onClick={this._showAddNewLanguageForm} />}
        </div>

        <Form onSubmit={handleSubmit}>

          <CompleteProfileDivider />

          <div>
            <CompleteProfileSectionTitle
              title={t('candidate.completeProfile.about.aboutYou')}
              description={t('candidate.completeProfile.about.aboutYouDescription')} />
            {
              !hasPitch && isNotNil(pitch) && (
                <div className='complete-profile-about__notification-container'>
                  <Notification iconClassName='icon-im-info-outline' messageText={t('candidate.completeProfile.about.aboutMeNotification')} warning />
                </div>
              )
            }
            <Field
              component={Editor}
              label={t('common.description')}
              name='pitch'
              limit={lengthLimit.pitch}
              placeholder={t('profile.additional.sections.aboutYou.description')}
            />
          </div>

          <CompleteProfileDivider />

          <div>
            <CompleteProfileSectionTitle title={''} />
            <div className='columns form__group form__wrapper--inline form__wrapper--inline-input'>
              <div className='column col-sm-12'>
                <Field
                  component={Select}
                  label={t('forms.countryOfResidence.label')}
                  options={this.props.countries}
                  placeholder={t('forms.countryOfResidence.placeholder')}
                  name='countryOfResidence'
                  withIcon
                />
              </div>
              <div className='column col-sm-12'>
                <Field
                  component={SearchMap}
                  label={t('forms.city.label')}
                  placeholder={t('forms.city.placeholder')}
                  selectedCountry={this.props.selectedCountry}
                  suggestTypes={['(cities)']}
                  single='city'
                  mapVisible={false}
                  name='city'
                />
              </div>
            </div>
            {/* <Fragment>
            <div className='columns form__wrapper--inline form__wrapper--inline-input'>
              <div className='column col-sm-12'>
                <Field
                  component={Select}
                  label={t('forms.nationality.label')}
                  options={this.props.nationalityOptions}
                  placeholder={t('forms.nationality.placeholder')}
                  name='nationality'
                  withIcon
                />
              </div>
            </div>
            </Fragment> */}
            {
              isNotNil(dateOfBirth) ? this.phoneInputField() : (
                <div className='columns form__wrapper--inline form__wrapper--inline-input'>
                  <div className='column col-sm-12'>
                    {this.ageField()}
                  </div>
                  <div className='column col-sm-12'>
                    {this.phoneInputField()}
                  </div>
                </div>
              )
            }
          </div>

          <CompleteProfileDivider />

          <div>
            <CompleteProfileSectionTitle title={''} />
            <Field
              component={Select}
              label={t('forms.visaStatus.label')}
              placeholder={t('forms.visaStatus.placeholder')}
              options={this.props.visaStatuses}
              name='visaStatus'
              withIcon
            />
            <div className='columns form__wrapper--inline columns form__wrapper--inline-radios'>
              <div className='column col-xs-12'>
                <RadioWrapper
                  legend={t('forms.drivingLicence.label')}
                >
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    text={t('common.yes')}
                    normalize={normalizeRadio}
                    name='driversLicence'
                    radioButtonValue
                  />
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    text={t('common.no')}
                    name='driversLicence'
                    normalize={normalizeRadio}
                    radioButtonValue={false}
                  />
                </RadioWrapper>
              </div>
              <div className='column col-xs-12'>
                <RadioWrapper
                  legend={t('forms.ownCar.label')}
                  disabled={!this.props.driversLicence}
                >
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    text={t('common.yes')}
                    normalize={normalizeRadio}
                    disabled={!this.props.driversLicence}
                    name='ownsACar'
                    radioButtonValue
                  />
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    text={t('common.no')}
                    normalize={normalizeRadio}
                    disabled={!this.props.driversLicence}
                    name='ownsACar'
                    radioButtonValue={false}
                  />
                </RadioWrapper>
              </div>
            </div>
          </div>

          <CompleteProfileDivider />

          <div>
            <CompleteProfileSectionTitle
              title={t('candidate.completeProfile.about.addAvailability')}
              description={t('candidate.completeProfile.about.addAvailabilityDescription')} />
            <div className='columns form__wrapper--inline'>
              <div className='column col-6 col-xs-12'>
                <Field
                  component={InputDatePicker}
                  label={t('forms.startDate.label')}
                  name='availableFrom'
                  icon={'ion-md-clock'}
                  floatRight
                  minDate={moment().utc()}
                  maxDate={moment().utc().add(3, 'months')}
                  placeholderText={t('forms.startDate.placeholder')}
                  format={value => convertDateToMoment(value)}
                  selectsStart
                  startDate={convertDateToMoment(startDate)}
                  endDate={convertDateToMoment(endDate)}
                />
              </div>
              <div className='column col-6 col-xs-12'>
                <Field
                  component={InputDatePicker}
                  label={t('forms.endDate.label')}
                  name='availableTo'
                  icon={'ion-md-clock'}
                  floatRight
                  minDate={moment().utc().add(1, 'days')}
                  maxDate={moment().utc().add(3, 'months')}
                  placeholderText={t('forms.endDate.placeholder')}
                  format={value => convertDateToMoment(value)}
                  selectsEnd
                  startDate={convertDateToMoment(startDate)}
                  endDate={convertDateToMoment(endDate)}
                />
              </div>
            </div>
          </div>

          <CompleteProfileDivider />

          <div>
            <CompleteProfileSectionTitle
              id='handicapDeclaration'
              title={t('profile.basics.handicapDeclaration.title')}
              description={this.getHandicapDeclarationDescription()}
            />
            <div className='columns'>
              <div className='column col-12'>
                <RadioWrapper
                  layout={'checkbox-vertical'}
                  hasRequiredError={hasHandicapDeclarationFieldError}
                  className='step_basic__radio-wrapper'
                  errorLabel={t('profile.basics.handicapDeclaration.title')}
                >
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    text={t('forms.handicapDeclaration.yes')}
                    name='handicapDeclaration'
                    radioButtonValue={handicapDeclarationType.isHandicapped}
                  />
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    text={t('forms.handicapDeclaration.no')}
                    name='handicapDeclaration'
                    radioButtonValue={handicapDeclarationType.isNotHandicapped}
                  />
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    text={t('forms.handicapDeclaration.notDisclose')}
                    name='handicapDeclaration'
                    radioButtonValue={handicapDeclarationType.undisclosed}
                  />
                </RadioWrapper>
              </div>
            </div>
            <div className='columns form__group--margin'>
              <div className='column col-12'>
                {
                  handicapDeclaration === handicapDeclarationType.isHandicapped && (
                    <Field
                      component={Textarea}
                      label={t('forms.handicapDeclaration.furtherInformation')}
                      name='handicapDetails'
                      rows={3}
                      placeholder={' '}
                      optional={t('common.optional')}
                    />
                  )
                }
              </div>
            </div>
          </div>

          <CompleteProfileDivider />

          <div>
            <CompleteProfileSectionTitle
              title={t('candidate.completeProfile.about.notifications')} />

            <Field
              component={SwitchToggle}
              label={t('forms.completeProfileAlert.placeholder')}
              name='profileCompletionNotificationEnabled'
              format={v => !!v}
            />
            <Field
              component={SwitchToggle}
              label={t('forms.interestingJobsAlert.placeholder')}
              name='relevantjobOffersNotificationEnabled'
              format={v => !!v}
            />
          </div>
        </Form>
        <DeleteModal
          title={t('remove.languageDetails.title')}
          content={t('remove.languageDetails.description')}
          handleDelete={this.deleteLanguageDetails('languageKey')}
          isModalVisible={this.state.deleteLanguageModalVisible}
          hideDeleteModal={this._closeDeleteLanguageModal}
        />

      </div>
    )
  }
}

export default withTranslations(CompleteProfileAboutForm)



