import startCase from 'lodash/startCase'
import { getEditorContentLength } from 'Shared/helpers/editor'
import { lengthLimit } from 'Shared/constants/Editor'
import { isNil } from 'ramda'
import moment from 'moment'
import { formats } from 'Shared/constants/Date'

export default values => {
  const errors = {}

  const nonNullableFields = [
    'availableFrom',
    'availableTo',
    'nationality',
    'countryOfResidence',
    'phoneNumber',
    'visaStatus',
    'handicapDeclaration',
    'dateOfBirth',
    'city'
  ]

  nonNullableFields.forEach(field => {
    if (isNil(values[field])) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  if (!values.pitch) {
    errors.pitch = {
      key: 'errors.descriptionIsARequiredField',
      options: {}
    }
  }

  if (values['pitch'] && getEditorContentLength(values['pitch']) < 30) {
    errors['pitch'] = {
      key: 'errors.descriptionTooShort',
      options: {
        field: startCase('pitch'),
        chars: startCase('30')
      }
    }
  }

  if (values['pitch'] && getEditorContentLength(values['pitch']) > lengthLimit.pitch) {
    errors['pitch'] = {
      key: 'errors.charactersLength',
      options: {
        limit: lengthLimit.pitch
      }
    }
  }

  if (moment(values['availableFrom'], formats.utc).endOf('day').isSame(moment(values['availableTo'], formats.utc).endOf('day'))) {
    errors['availableFrom'] = {
      key: 'errors.dateSame'
    }
    errors['availableTo'] = {
      key: 'errors.dateSame'
    }
  }

  if (moment(values['availableTo'], formats.utc).endOf('day').isBefore(moment(values['availableFrom'], formats.utc).endOf('day'))) {
    errors['availableTo'] = {
      key: 'errors.dateLater'
    }
  }

  if (moment(values['availableFrom'], formats.utc).endOf('day').isBefore(moment())) {
    errors['availableFrom'] = {
      key: 'errors.dateLater'
    }
  }

  if (moment(values['availableTo'], formats.utc).endOf('day').isBefore(moment())) {
    errors['availableTo'] = {
      key: 'errors.dateLater'
    }
  }

  if (!values['availableFrom'] && values['availableTo']) {
    errors['availableFrom'] = {
      key: 'errors.requiredField',
      options: {
        field: startCase('availableFrom')
      }
    }
  }

  if (!values['availableTo'] && values['availableFrom']) {
    errors['availableTo'] = {
      key: 'errors.requiredField',
      options: {
        field: startCase('availableTo')
      }
    }
  }

  return errors
}



