import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Collapse, { Panel } from 'rc-collapse'
import { pathOr } from 'ramda'
import { AddEducationDetailsFormConnector, EducationDetailsFormConnector } from 'Candidate/connectors/Forms/Profile/Education/index'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton/index'
import DeleteModal from 'Shared/components/DeleteModal/index'

import withTranslations from 'Shared/hoc/withTranslations/index'

class ProfileEducationUniversity extends Component {
  state = {
    educationKey: null,
    deleteEducationModalVisible: false,
    educationDetailsRemoveId: '',
    universityFormVisible: false
  }

  componentWillReceiveProps ({hasAddUniversitySucceeded}) {
    hasAddUniversitySucceeded && this.hideEducationDetailsForm()
  }

  componentDidMount () {
    this.props.openAddAsDefault && this.showEducationDetailsForm()
  }

  showEducationDetailsForm = () => this.setState({ universityFormVisible: true, educationKey: '99' })
  hideEducationDetailsForm = () => this.setState({ universityFormVisible: false })

  setActiveAccordionKey = educationKey => this.setState({ educationKey })
  closeAccordionByKey = () => this.setState({ educationKey: null })

  _openDeleteEducationModal = educationId => () => this.setState({ educationDetailsRemoveId: educationId, deleteEducationModalVisible: true })
  _closeDeleteEducationModal = () => this.setState({ educationDetailsRemoveId: '', deleteEducationModalVisible: false })

  deleteEducationDetails = async () => {
    await this.props.deleteEducationDetailsById(this.state.educationDetailsRemoveId)

    this.closeAccordionByKey()
    return this._closeDeleteEducationModal()
  }

  renderCandidateEducation = () => {
    const { educationDetails } = this.props
    return educationDetails.map((detail, i) => (
      <Panel key={i + 1} header={pathOr(detail.universityName, ['university', 'name'], detail)}>
        <EducationDetailsFormConnector
          key={detail.id}
          details={detail}
          closeOnSubmit={this.closeAccordionByKey}
          handleDelete={this._openDeleteEducationModal(detail.id)}
        />
      </Panel>
    ))
  }

  render () {
    const { t } = this.props

    return (
      <div>
        <Collapse
          accordion
          className='education-details__collapse'
          activeKey={this.state.educationKey}
          onChange={this.setActiveAccordionKey}
        >
          {this.renderCandidateEducation()}
          {this.state.universityFormVisible && (
            <Panel key={'99'} header={t('profile.education.sections.educationDetails.addNew')}>
              <AddEducationDetailsFormConnector
                closeOnSubmit={this.closeAccordionByKey}
                handleDelete={this.hideEducationDetailsForm}
              />
            </Panel>
          )}
        </Collapse>
        {
          !this.state.universityFormVisible &&
          <AddNewSectionButton
            text={t('profile.education.sections.educationDetails.addNew')}
            onClick={this.showEducationDetailsForm}
          />
        }
        <DeleteModal
          title={t('remove.educationDetails.title')}
          content={t('remove.educationDetails.description')}
          handleDelete={this.deleteEducationDetails}
          isModalVisible={this.state.deleteEducationModalVisible}
          hideDeleteModal={this._closeDeleteEducationModal}
        />
      </div>
    )
  }
}

ProfileEducationUniversity.propTypes = {
  educationDetails: PropTypes.array.isRequired,
  hasAddUniversitySucceeded: PropTypes.bool,
  deleteEducationDetailsById: PropTypes.func.isRequired
}

export default withTranslations(ProfileEducationUniversity)



