import React, { Component } from 'react'
import { Switch, Route, Redirect, withRouter } from 'react-router-dom'
import { completeProfileSteps } from 'Shared/constants/Profile'
import CompleteProfileNavigationConnector from 'Candidate/connectors/CompleteProfile/CompleteProfileNavigationConnector'
import CompleteProfileStepHeading from 'Candidate/components/CompleteProfileStepHeading'
import CompleteProfilePreferencesFormConnector from 'Candidate/connectors/Forms/CompleteProfile/Preferences/CompleteProfilePreferencesFormConnector'
import CompleteProfileEducationFormConnector from 'Candidate/connectors/Forms/CompleteProfile/Education/CompleteProfileEducationFormConnector'
import CompleteProfileExperienceFormConnector from 'Candidate/connectors/Forms/CompleteProfile/Experience/CompleteProfileExperienceFormConnector'
import CompleteProfileAboutFormConnector from 'Candidate/connectors/Forms/CompleteProfile/About/CompleteProfileAboutFormConnector'
import FooterNavigationConnector from 'Candidate/connectors/ProfileNavigation/FooterNavigationConnector'
import * as R from 'ramda'
import './CompleteProfileView.scss'
import { getNextStepPath, getPreviousStepPath } from 'Shared/helpers/profile'

class CompleteProfileView extends Component {
  getStepName = R.pipe(
    R.pathOr('', ['location', 'pathname']),
    R.split('/'),
    R.last
  )
  getStepIdByName = stepName => R.pipe(
    R.find(R.propEq('name', stepName)),
    R.propOr(1, 'id')
  )(completeProfileSteps)

  getStepId = () => R.pipe(
    this.getStepName,
    this.getStepIdByName
  )(this.props)

  saveAndGoNext = () => this.props.history.push(getNextStepPath(this.getStepId()))
  goPrevious = () => this.props.history.push(getPreviousStepPath(this.getStepId()))

  render () {
    return (
      <div className='complete-profile-view'>
        <CompleteProfileNavigationConnector />
        <div className='container complete-profile-view__container parsing--hidden'>
          <CompleteProfileStepHeading />
          <Switch>
            <Route exact path='/candidate/profile/complete/preferences' component={CompleteProfilePreferencesFormConnector} />
            <Route exact path='/candidate/profile/complete/education' component={CompleteProfileEducationFormConnector} />
            <Route exact path='/candidate/profile/complete/experience' component={CompleteProfileExperienceFormConnector} />
            <Route exact path='/candidate/profile/complete/about' component={CompleteProfileAboutFormConnector} />
            <Redirect from='/candidate/profile/complete' to='/candidate/profile/complete/preferences' />
          </Switch>
          <FooterNavigationConnector
            currentStep={this.getStepId()}
            saveAndGoNext={this.saveAndGoNext}
            goPrevious={this.getStepId() !== 1 && this.goPrevious}
          />
        </div>
      </div>
    )
  }
}

export default withRouter(CompleteProfileView)



