import { connect } from 'react-redux'
import UploadedFilesList from 'Shared/components/UploadedFilesList/UploadedFilesList'
import { getCandidateCvFiles } from 'Shared/selectors/files'
import { UPLOAD_DOCUMENT_TYPES } from 'Shared/constants/Files'
import { deleteCandidateFileById } from 'Shared/ducks/files'

const mapStateToProps = state => ({
  candidateAttachedCvs: state.files.candidateAttachedCvs,
  files: getCandidateCvFiles(state),
  initialFileType: UPLOAD_DOCUMENT_TYPES.CV
})

const mapDispatchToProps = dispatch => ({
  deleteFile: (fileId, name) => dispatch(deleteCandidateFileById(fileId, name))
})

export default connect(mapStateToProps, mapDispatchToProps)(UploadedFilesList)



