import React from 'react'
import { NavLink } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import cx from 'classnames'
import './CompleteProfileStep.scss'

const CompleteProfileStep = ({ t, id, name, isStepCompleted }) => {
  const stepClass = cx({
    'complete-profile-step': true,
    'complete-profile-step--completed': isStepCompleted
  })

  return (
    <div id={name} className={stepClass}>
      <NavLink to={`/candidate/profile/complete/${name}`}>
        { isStepCompleted && <i className='icon-im-step-done complete-profile-step__icon' /> }
        <div className='complete-profile-step__name'>{id}. {t(`profile.${name}.title`)}</div>
      </NavLink>
    </div>
  )
}

export default withTranslations(CompleteProfileStep)



