import { reduxForm, getFormValues, isInvalid, getFormSyncErrors } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { t } from 'i18next'
import ValidationHelperForm from 'Candidate/forms/Profile/ValidationHelperForm'
import { getCandidateCvFiles } from 'Shared/selectors/files'
import { scrollToFirstError } from 'Shared/helpers/scroll'
import { completeProfileErrors } from 'Shared/selectors/reduxForm'

export const formName = 'CompleteProfile-cvDocumentValidationHelperForm'
const fieldName = `validationValue-for-${formName}`

let Form = reduxForm({
  form: formName,
  enableReinitialize: true,
  onSubmit: values => {},
  onSubmitFail: (errors, dispatch, submitError, props) => {
    return scrollToFirstError(props.completeProfileErrors)
  },
  validate: values => {
    const errors = {}

    if (values[fieldName] === 'invalid') {
      errors[fieldName] = {
        key: 'errors.atLeastOneField',
        options: { field: t('common.cv') }
      }
    }
    return errors
  }
})(ValidationHelperForm)

const mapStateToProps = (state) => {
  const cvFiles = getCandidateCvFiles(state)

  return ({
    isFormInvalid: isInvalid(formName)(state),
    validationHelperValues: getFormValues(formName)(state),
    validationHelperErrors: getFormSyncErrors(formName)(state),
    completeProfileErrors: completeProfileErrors(state),
    initialValues: {
      [fieldName]: cvFiles.length === 0 ? 'invalid' : 'valid'
    }
  })
}

export default withRouter(connect(mapStateToProps)(Form))



