import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Collapse, { Panel } from 'rc-collapse'
import { pathOr } from 'ramda'
import {
  ExperienceDetailsFormConnector,
  AddExperienceDetailsFormConnector,
  VolunteeringDetailsFormConnector,
  AddVolunteeringDetailsFormConnector,
  WorkExperienceFormConnector
} from 'Candidate/connectors/Forms/Profile/Experience'
import SkillsFormConnector from 'Candidate/connectors/Forms/Profile/Experience/SkillsFormConnector'
import ExperienceValidationHelperFormConnector from 'Candidate/connectors/Forms/Profile/Experience/ExperienceValidationHelperFormConnector'
import VolunteeringValidationHelperFormConnector from 'Candidate/connectors/Forms/Profile/Experience/VolunteeringValidationHelperFormConnector'
import CompleteProfileSectionTitle from 'Candidate/components/CompleteProfileSectionTitle'
import CompleteProfileDivider from 'Candidate/components/CompleteProfileDivider'
import withTranslations from 'Shared/hoc/withTranslations'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import DeleteModal from 'Shared/components/DeleteModal'
import './CompleteProfileExperienceForm.scss'
import SelectOptionConfirmation from 'Shared/components/SelectOptionConfirmation/SelectOptionConfirmation'

class ProfileExperience extends Component {
  static propTypes = {
    t: PropTypes.func.isRequired,
    hasWorkExperience: PropTypes.bool,
    experiences: PropTypes.array,
    volunteering: PropTypes.array,
    hasAddExperienceDetailsSucceeded: PropTypes.bool,
    hasAddVolunteeringDetailsSucceeded: PropTypes.bool
  }

  state = {
    experienceRemoveId: '',
    volunteeringRemoveId: '',
    addNewExperienceFormVisible: false,
    addNewVolunteeringFormVisible: false,
    showDeleteExperienceModal: false,
    showDeleteVolunteeringModal: false,
    experienceKey: null,
    volunteeringKey: null,
    isAutoApproveExperiencePeriodConfirmationVisible: false,
    isAutoApproveExperiencePeriodConfirmationLoading: false
  }

  handleOpenAutoApproveExperiencePeriodConfirmation = () => this.setState({ isAutoApproveExperiencePeriodConfirmationVisible: true })
  handleCloseAutoApproveExperiencePeriodConfirmation = () => this.setState({ isAutoApproveExperiencePeriodConfirmationVisible: false })

  componentDidUpdate (prevProps) {
    if (prevProps.hasMoreThan6YearsOfExperience !== this.props.hasMoreThan6YearsOfExperience && this.props.hasMoreThan6YearsOfExperience === true) {
      this.handleOpenAutoApproveExperiencePeriodConfirmation()
    }
  }

  handleDeleteAccount = () => {
    this.setState({ isAutoApproveExperiencePeriodConfirmationLoading: true })
    this.props.deleteAccount()
  }

  componentWillReceiveProps (nextProps) {
    nextProps.hasAddExperienceDetailsSucceeded && this._hideAddNewExperienceForm()
    nextProps.hasAddVolunteeringDetailsSucceeded && this._hideAddNewVolunteeringForm()
  }

  setActiveAccordionKey = key => keyValue => this.setState({ [key]: keyValue })
  closeAccordionByKey = key => () => this.setState({ [key]: null })

  _showAddNewExperienceForm = () => this.setState({ addNewExperienceFormVisible: true, experienceKey: '99' })
  _hideAddNewExperienceForm = () => this.setState({ addNewExperienceFormVisible: false })

  _showAddNewVolunteeringForm = () => this.setState({ addNewVolunteeringFormVisible: true, volunteeringKey: '99' })
  _hideAddNewVolunteeringForm = () => this.setState({ addNewVolunteeringFormVisible: false })

  _openDeleteExperienceModal = experienceId => () => this.setState({ experienceRemoveId: experienceId, showDeleteExperienceModal: true })
  _closeDeleteExperienceModal = () => this.setState({ experienceRemoveId: '', showDeleteExperienceModal: false })

  _openDeleteVolunteeringModal = volunteeringId => () => this.setState({ volunteeringRemoveId: volunteeringId, showDeleteVolunteeringModal: true })
  _closeDeleteVolunteeringModal = () => this.setState({ volunteeringRemoveId: '', showDeleteVolunteeringModal: false })

  deleteExperienceDetails = accordionKey => async () => {
    await this.props.deleteExperience(this.state.experienceRemoveId)

    this.closeAccordionByKey(accordionKey)()
    this._closeDeleteExperienceModal()
  }

  deleteVolunteeringDetails = accordionKey => async () => {
    await this.props.deleteExperience(this.state.volunteeringRemoveId)

    this.closeAccordionByKey(accordionKey)()
    this._closeDeleteVolunteeringModal()
  }

  renderCandidateExperience () {
    const { experiences } = this.props
    return experiences.map((experience, i) => {
      // TODO this pathOr is only for old test data, which has organisations as object.
      // @bach 3.03.2018
      const organisationName = pathOr(experience.organisationName, ['organisation', 'name'])(experience)
      return (
        <Panel key={i + 1} header={organisationName}>
          <ExperienceDetailsFormConnector
            key={experience.id}
            closeOnSubmit={this.closeAccordionByKey('experienceKey')}
            handleDelete={this._openDeleteExperienceModal(experience.id)}
            details={{ ...experience, organisationName }}
          />
        </Panel>
      )
    })
  }

  renderCandidateVolunteering () {
    const { volunteering } = this.props
    return volunteering.map((volunteering, i) => {
      // TODO this pathOr is only for old test data, which has organisations as object.
      // @bach 3.03.2018
      const organisationName = pathOr(volunteering.organisationName, ['organisation', 'name'])(volunteering)

      return (
        <Panel key={i + 1} header={organisationName}>
          <VolunteeringDetailsFormConnector
            key={volunteering.id}
            closeOnSubmit={this.closeAccordionByKey('volunteeringKey')}
            handleDelete={this._openDeleteVolunteeringModal(volunteering.id)}
            details={{ ...volunteering, organisationName }}
          />
        </Panel>
      )
    })
  }

  render () {
    const { t } = this.props

    return (
      <div className='complete-profile-experience'>
        <div>
          <CompleteProfileSectionTitle title={t('candidate.completeProfile.experience.addWorkExperience')} />
          <section>
            <WorkExperienceFormConnector />
            {this.props.hasWorkExperience &&
            <div>
              <ExperienceValidationHelperFormConnector />
              <Collapse accordion className='education-details__collapse' activeKey={this.state.experienceKey} onChange={this.setActiveAccordionKey('experienceKey')}>
                {this.renderCandidateExperience()}
                {this.state.addNewExperienceFormVisible && (
                  <Panel key={'99'} header={t('profile.experience.sections.workExperience.addNew')}>
                    <AddExperienceDetailsFormConnector
                      closeOnSubmit={this.closeAccordionByKey('experienceKey')}
                      handleDelete={this._hideAddNewExperienceForm}
                    />
                  </Panel>
                )}
              </Collapse>
              {!this.state.addNewExperienceFormVisible &&
              <AddNewSectionButton text={t('profile.experience.sections.workExperience.addNew')} onClick={this._showAddNewExperienceForm} />}
            </div>}
          </section>
          <CompleteProfileDivider />
          <CompleteProfileSectionTitle title={t('candidate.completeProfile.experience.addSkills')} />
          <SkillsFormConnector />
          <CompleteProfileDivider />
          <CompleteProfileSectionTitle
            title={t('candidate.completeProfile.experience.addVolunteering')}
            description={t('candidate.completeProfile.experience.addVolunteeringDescription')}
          />
          <section>
            <VolunteeringValidationHelperFormConnector />
            <Collapse accordion className='education-details__collapse' activeKey={this.state.volunteeringKey} onChange={this.setActiveAccordionKey('volunteeringKey')}>
              {this.renderCandidateVolunteering()}
              {this.state.addNewVolunteeringFormVisible &&
              <Panel key={'99'} header={t('profile.experience.sections.volunteering.addNew')}>
                <AddVolunteeringDetailsFormConnector
                  closeOnSubmit={this.closeAccordionByKey('volunteeringKey')}
                  handleDelete={this._hideAddNewVolunteeringForm}
                />
              </Panel>}
            </Collapse>
            {!this.state.addNewVolunteeringFormVisible &&
            <AddNewSectionButton text={t('profile.experience.sections.volunteering.addNew')} onClick={this._showAddNewVolunteeringForm} />}
          </section>

        </div>
        <DeleteModal
          title={t('remove.experienceDetails.title')}
          content={t('remove.experienceDetails.description')}
          handleDelete={this.deleteExperienceDetails('experienceKey')}
          isModalVisible={this.state.showDeleteExperienceModal}
          hideDeleteModal={this._closeDeleteExperienceModal}
        />
        <DeleteModal
          title={t('remove.volunteeringDetails.title')}
          content={t('remove.volunteeringDetails.description')}
          handleDelete={this.deleteVolunteeringDetails('volunteeringKey')}
          isModalVisible={this.state.showDeleteVolunteeringModal}
          hideDeleteModal={this._closeDeleteVolunteeringModal}
        />
        <SelectOptionConfirmation
          title={t('profile.experience.autoApproveConfirmation.title')}
          description={t('profile.experience.autoApproveConfirmation.description')}
          acceptText={t('profile.experience.autoApproveConfirmation.options.approve')}
          declineText={t('profile.experience.autoApproveConfirmation.options.decline')}
          show={this.state.isAutoApproveExperiencePeriodConfirmationVisible}
          handleClose={this.handleCloseAutoApproveExperiencePeriodConfirmation}
          handleAccept={this.handleCloseAutoApproveExperiencePeriodConfirmation}
          handleDecline={this.handleDeleteAccount}
          isLoading={this.state.isAutoApproveExperiencePeriodConfirmationLoading}
        />
      </div>
    )
  }
}

export default withTranslations(ProfileExperience)



