import { connect } from 'react-redux'
import { reduxForm } from 'redux-form'
import { withRouter } from 'react-router-dom'
import LanguageDetails from 'Candidate/forms/Profile/LanguageDetails'
import { addCandidateLanguage } from 'Shared/ducks/languages'
import validate from './LanguageFormValidator'
import { getAvailableCandidateLanguagesOptions } from 'Shared/selectors/languages'
import { scrollToFirstError } from 'Shared/helpers/scroll'

export const formName = 'CompleteProfile-AddLanguageForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmitFail: scrollToFirstError,
  enableReinitialize: true
})(LanguageDetails)

const mapStateToProps = (state, props) => ({
  initialValues: {
    candidateId: state.user.details.id,
    speaking: 0,
    writing: 0,
    reading: 0
  },
  languageOptions: getAvailableCandidateLanguagesOptions(state.user.details.id)(state)
})

const mapDispatchToProps = dispatch => ({
  onFormSubmit: values => dispatch(addCandidateLanguage(values))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



