// The description is generated basing on the education level and education graduate date. If section is not provided,
// the sentence should be removed or cut in a grammatically correct way.
// The description is generated by following templates:
//
// 1) UNIVERSITIES
//
// * FUTURE GRADUATION
// Hi, my name is <full_name>. I’m a <major> student, working on my <degree_type> from <university_name>,
// graduating in <grad_month> <grad_year>. I’m looking for <preferred_employment_type(s)> opportunities
// in the field of <preferred_department_type(s)> in <preferred_location(s)>. I am fluent in <fluent_speaking_language(s)>
// and have previous experience as a <job_title in most recent position> at <company_name for most recent position>.
// If you would like to learn more, please don’t hesitate to reach out on Oliv.
//
// * ALREADY GRADUATED
// Hi, my name is <full_name>. With previous experience as a <job_title at most recent company> at <company_name for most recent position>,
// ’m looking out for <preferred_employment_type(s)> opportunities in the field of <preferred_department_type(s)>
// in <preferred_location(s)>. Additionally, I graduated with a <degree_type> in <major> from <university_name> in <grad_month> <grad_year>
// and am fluent in <fluent_speaking_language(s)>. If you’d like to learn more, please don’t hesitate to reach out to me on Oliv.
//
// 2) HIGH SCHOOLS
//
// * FUTURE GRADUATION
// Hi, my name is <full_name>. I’m a student from <school_name>, graduating in <grad_month> <grad_year>.
// I’m looking for <preferred_employment_type(s)> opportunities in the field of <preferred_department_type(s)>
// in <preferred_location(s)>. I am fluent in <fluent_speaking_language(s)> and have previous experience as a
// <job_title in most recent position> at <company_name for most recent position>. If you would like to learn
// more, please don’t hesitate to reach out on Oliv.
//
// * ALREADY GRADUATED
// Hi, my name is <full_name>. I’m a student from <school_name>, graduated in <grad_month> <grad_year>.
// I’m looking for <preferred_employment_type(s)> opportunities in the field of <preferred_department_type(s)> in
// <preferred_location(s)>. I am fluent in <fluent_speaking_language(s)> and have previous experience as
// a <job_title in most recent position> at <company_name for most recent position>. If you would like
// to learn more, please don’t hesitate to reach out on Oliv.
//

import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import { getUserId, isHighSchoolLevelEducation, userFullName } from 'Shared/selectors/user'
import { isNotEmpty } from 'Shared/helpers/Ramda'
import { isStillAvailable, formatByMonthYear } from 'Shared/helpers/date'
import { getUserTranslationsOrDefault } from 'Shared/helpers/translations'
import LanguageService from 'Shared/services/LanguageService'
import * as R from 'ramda'
import { ISOcodes } from 'Shared/constants/i18n'
import { EMPLOYMENT_TYPES } from 'Shared/constants/JobPreferences'
import { joinWithCommasOrLinkingWordIfNeeded, withBeforeNoun } from 'Shared/helpers/string'
import { experienceTypes } from 'Shared/constants/Experience'

const getUserTranslationsOrDefaultFunc = (languageIsoCode, exclude = []) => item => getUserTranslationsOrDefault(item, languageIsoCode, exclude)

const userDetails = state => state.user.details
const universities = state => state.education.entries
const highSchools = state => state.highSchoolLevelEducation.entries
const departments = state => state.departments.candidate
const locations = state => state.locations.candidate
const languages = state => state.languages.entries
const experiences = state => state.experiences.entries
const degrees = state => state.resources.degrees.entries
const languageResource = state => state.resources.languages.entries

const hasAnyEntriesById = (entries, userId) => R.pipe(
  transformToArray,
  R.filter(
    R.propEq('candidateId', userId)
  ),
  isNotEmpty
)(entries)

export const hasAnyExperience = createSelector(
  experiences,
  userDetails,
  getUserId,
  (experiences, userDetails, userId) => {
    const hasWorkExperience = R.propOr(false, 'hasWorkExperience', userDetails)
    const employment = () => R.pipe(
      transformToArray,
      R.filter(
        R.allPass([
          R.propEq('candidateId', userId),
          R.propEq('type', experienceTypes.employment)
        ])
      )
    )(experiences)

    const volunteering = () => R.pipe(
      transformToArray,
      R.filter(
        R.allPass([
          R.propEq('candidateId', userId),
          R.pipe(
            R.propEq('type', experienceTypes.employment),
            R.not
          )
        ])
      )
    )(experiences)

    return R.pipe(
      R.ifElse(
        R.equals(true),
        employment,
        volunteering
      ),
      R.isEmpty,
      R.not
    )(hasWorkExperience)
  }
)

export const hasAnyEducation = createSelector(
  universities,
  highSchools,
  isHighSchoolLevelEducation,
  getUserId,
  (universities, highSchools, isHighSchoolLevelEducation, userId) => R.ifElse(
    R.equals(true),
    () => hasAnyEntriesById(highSchools, userId),
    () => hasAnyEntriesById(universities, userId)
  )(isHighSchoolLevelEducation)
)

export const hasAnyPreferences = createSelector(
  departments,
  locations,
  userDetails,
  getUserId,
  (departments, locations, userDetails, userId) => {
    const hasDepartments = isNotEmpty(departments)
    const hasLocations = hasAnyEntriesById(locations, userId)
    const hasEmploymentTypes = R.pipe(
      R.pick([
        'interestedInInternship',
        'interestedInGraduateJob',
        'interestedInVolunteering',
        'interestedInEventPromotion'
      ]),
      R.values,
      R.any(
        R.equals(true)
      )
    )(userDetails)

    return hasDepartments && hasLocations && hasEmploymentTypes
  }
)

export const hasAnyFluentLanguage = createSelector(
  languages,
  getUserId,
  (languages, userId) => R.pipe(
    transformToArray,
    R.filter(
      R.allPass([
        R.propEq('candidateId', userId),
        R.propEq('reading', 3),
        R.propEq('speaking', 3),
        R.propEq('writing', 3)
      ])
    ),
    isNotEmpty
  )(languages)
)

const isMostRecentEducationFinished = id => R.pipe(
  transformToArray,
  R.filter(
    R.propEq('candidateId', id)
  ),
  R.sortBy(R.propEq('graduatedAt')),
  R.last,
  R.propOr('', 'graduatedAt'),
  R.ifElse(
    R.isEmpty,
    R.always(false),
    R.pipe(
      isStillAvailable,
      R.not
    )
  )
)

export const hasFinishedMostRecentEducation = createSelector(
  universities,
  highSchools,
  isHighSchoolLevelEducation,
  getUserId,
  (universities, highSchools, isHighSchoolLevelEducation, userId) => R.ifElse(
    R.equals(true),
    () => isMostRecentEducationFinished(userId)(highSchools),
    () => isMostRecentEducationFinished(userId)(universities)
  )(isHighSchoolLevelEducation)
)

const lastEducation = id => R.pipe(
  transformToArray,
  R.filter(
    R.propEq('candidateId', id)
  ),
  R.sortBy(R.propEq('graduatedAt')),
  R.last
)

export const universityMajor = createSelector(
  universities,
  getUserId,
  (universities, userId) => R.pipe(
    lastEducation(userId),
    R.ifElse(
      R.isNil,
      R.always(''),
      R.pipe(
        R.propOr({}, 'major'),
        getUserTranslationsOrDefaultFunc(ISOcodes[LanguageService.userLanguage]),
        R.propOr('', 'name')
      )
    )
  )(universities)
)

const getDegreeNameByCode = degrees => code => R.pipe(
  transformToArray,
  R.find(R.propEq('degree_code', code)),
  R.propOr(code, 'name')
)(degrees)

const degreeNameMap = {
  undergraduate_student: 'Undergraduate Degree',
  postgraduate_student: 'Postgraduate Degree '
}

const convertToPropName = R.pipe(
  R.toLower,
  R.replace(' ', '_')
)

const mapNameIfNeeded = name => R.propOr(name, convertToPropName(name))(degreeNameMap)

export const universityDegree = createSelector(
  universities,
  degrees,
  getUserId,
  (universities, degrees, userId) => R.pipe(
    lastEducation(userId),
    R.ifElse(
      R.isNil,
      R.always(''),
      R.pipe(
        R.propOr('', 'degree'),
        getDegreeNameByCode(degrees),
        mapNameIfNeeded
      )
    )
  )(universities)
)

export const universityName = createSelector(
  universities,
  getUserId,
  (universities, userId) => R.pipe(
    lastEducation(userId),
    R.ifElse(
      R.isNil,
      R.always(''),
      R.pathOr('', ['university', 'name'])
    )
  )(universities)
)

const getGraduationDate = id => R.pipe(
  lastEducation(id),
  R.ifElse(
    R.isNil,
    R.always(''),
    R.pipe(
      R.propOr('', 'graduatedAt'),
      formatByMonthYear
    )
  )
)

export const universityGraduationDate = createSelector(
  universities,
  getUserId,
  (universities, userId) => getGraduationDate(userId)(universities)
)

export const highSchoolName = createSelector(
  highSchools,
  getUserId,
  (highSchools, userId) => R.pipe(
    lastEducation(userId),
    R.ifElse(
      R.isNil,
      R.always(''),
      R.pathOr('', ['highSchool', 'name'])
    )
  )(highSchools)
)

export const highSchoolGraduationDate = createSelector(
  highSchools,
  getUserId,
  (highSchools, userId) => getGraduationDate(userId)(highSchools)
)

const getEmploymentTypeObject = employmentPreference => R.find(R.propEq('_id', employmentPreference))(EMPLOYMENT_TYPES)

export const employmentType = createSelector(
  userDetails,
  R.pipe(
    R.pick([
      'interestedInInternship',
      'interestedInGraduateJob',
      'interestedInVolunteering',
      'interestedInEventPromotion'
    ]),
    R.pickBy(
      R.equals(true)
    ),
    R.keys,
    R.map(
      R.pipe(
        getEmploymentTypeObject,
        getUserTranslationsOrDefaultFunc(ISOcodes[LanguageService.userLanguage]),
        R.propOr('', 'name')
      )
    ),
    joinWithCommasOrLinkingWordIfNeeded('and/or')
  )
)

const departmentNameMap = {
  administrative: 'Administration',
  legal: 'Law'
}

const mapNames = name => R.propOr(name, R.toLower(name))(departmentNameMap)

export const departmentType = createSelector(
  departments,
  R.pipe(
    transformToArray,
    R.map(
      R.pipe(
        getUserTranslationsOrDefaultFunc(ISOcodes[LanguageService.userLanguage]),
        R.propOr('', 'description'),
        mapNames
      )
    ),
    joinWithCommasOrLinkingWordIfNeeded('or')
  )
)

export const preferredLocation = createSelector(
  locations,
  getUserId,
  (locations, userId) => R.pipe(
    transformToArray,
    R.filter(
      R.propEq('candidateId', userId)
    ),
    R.map(
      R.propOr('', 'city')
    ),
    joinWithCommasOrLinkingWordIfNeeded('or')
  )(locations)
)

export const fluentLanguages = createSelector(
  languages,
  languageResource,
  getUserId,
  (languages, languageResource, userId) => R.pipe(
    transformToArray,
    R.filter(
      R.allPass([
        R.propEq('candidateId', userId),
        R.propEq('reading', 3),
        R.propEq('speaking', 3),
        R.propEq('writing', 3)
      ])
    ),
    R.map(
      R.pipe(
        R.propOr('', 'languageCode'),
        code => R.pathOr('', [code, 'language'], languageResource)
      )
    ),
    joinWithCommasOrLinkingWordIfNeeded('and')
  )(languages)
)

const getLastExperience = (userId, hasWorkExperience) => R.pipe(
  transformToArray,
  R.filter(
    R.allPass([
      R.propEq('candidateId', userId),
      R.ifElse(
        hasWorkExperience,
        R.propEq('type', experienceTypes.employment),
        R.pipe(
          R.propEq('type', experienceTypes.employment),
          R.not
        )
      )
    ])
  ),
  R.sort(
    (a, b) => {
      const getDateOrCurrent = R.pipe(
        R.prop('finishedAt'),
        R.ifElse(
          R.isNil,
          R.always(new Date()),
          e => new Date(e)
        )
      )

      return R.comparator((a, b) => R.gt(b, a))(getDateOrCurrent(a), getDateOrCurrent(b))
    }
  ),
  R.last
)

export const experiencePosition = createSelector(
  experiences,
  userDetails,
  getUserId,
  (experiences, userDetails, userId) => {
    const hasWorkExperience = () => R.propOr(false, 'hasWorkExperience', userDetails)

    return R.pipe(
      getLastExperience(userId, hasWorkExperience),
      R.propOr('', 'position')
    )(experiences)
  }
)

export const experienceOrganisationName = createSelector(
  experiences,
  userDetails,
  getUserId,
  (experiences, userDetails, userId) => {
    const hasWorkExperience = () => R.propOr(false, 'hasWorkExperience', userDetails)

    return R.pipe(
      getLastExperience(userId, hasWorkExperience),
      R.propOr('', 'organisationName')
    )(experiences)
  }
)

export const experienceIsCurrent = createSelector(
  experiences,
  userDetails,
  getUserId,
  (experiences, userDetails, userId) => {
    const hasWorkExperience = () => R.propOr(false, 'hasWorkExperience', userDetails)

    return R.pipe(
      getLastExperience(userId, hasWorkExperience),
      R.propOr(null, 'finishedAt'),
      R.isNil
    )(experiences)
  }
)

export const descriptionFullName = createSelector(
  userFullName,
  fullName => `Hi, my name is ${fullName}.`
)

export const descriptionEducation = createSelector(
  hasAnyEducation,
  hasFinishedMostRecentEducation,
  isHighSchoolLevelEducation,
  universityMajor,
  universityDegree,
  universityName,
  universityGraduationDate,
  highSchoolName,
  highSchoolGraduationDate,
  hasAnyPreferences,
  hasAnyFluentLanguage,
  (hasAnyEducation,
    hasFinishedMostRecentEducation,
    isHighSchoolLevelEducation,
    universityMajor,
    universityDegree,
    universityName,
    universityGraduationDate,
    highSchoolName,
    highSchoolGraduationDate,
    hasAnyPreferences,
    hasAnyFluentLanguage
  ) => {
    switch (true) {
      case hasAnyEducation && !hasFinishedMostRecentEducation && !isHighSchoolLevelEducation:
        return (
          ` I'm ${withBeforeNoun(universityMajor)} student, working on my ${universityDegree} from ${universityName}, graduating in ${universityGraduationDate}.`
        )
      case hasAnyEducation && hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && hasAnyPreferences && hasAnyFluentLanguage:
        return (
          ` Additionally, I graduated with ${withBeforeNoun(universityDegree)} in ${universityMajor} from ${universityName} in ${universityGraduationDate}`
        )
      case hasAnyEducation && hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && !hasAnyPreferences && hasAnyFluentLanguage:
        return (
          ` I graduated with ${withBeforeNoun(universityDegree)} in ${universityMajor} from ${universityName} in ${universityGraduationDate}`
        )
      case hasAnyEducation && hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && hasAnyPreferences && !hasAnyFluentLanguage:
        return (
          ` Additionally, I graduated with ${withBeforeNoun(universityDegree)} in ${universityMajor} from ${universityName} in ${universityGraduationDate}.`
        )
      case hasAnyEducation && hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && !hasAnyPreferences && !hasAnyFluentLanguage:
        return (
          ` I graduated with ${withBeforeNoun(universityDegree)} in ${universityMajor} from ${universityName} in ${universityGraduationDate}.`
        )
      case hasAnyEducation && !hasFinishedMostRecentEducation && isHighSchoolLevelEducation:
        return (
          ` I'm a student from ${highSchoolName}, graduating in ${highSchoolGraduationDate}.`
        )
      case hasAnyEducation && hasFinishedMostRecentEducation && isHighSchoolLevelEducation:
        return (
          ` I'm a student from ${highSchoolName}, graduated in ${highSchoolGraduationDate}.`
        )
      default:
        return ''
    }
  }
)

export const descriptionPreferences = createSelector(
  hasAnyPreferences,
  employmentType,
  departmentType,
  preferredLocation,
  (hasAnyPreferences,
    employmentType,
    departmentType,
    preferredLocation) => {
    switch (true) {
      case hasAnyPreferences:
        return (
          ` I'm looking for ${employmentType} opportunities in the field of ${departmentType} in ${preferredLocation}.`
        )
      default:
        return ''
    }
  }
)

export const descriptionLanguage = createSelector(
  hasAnyEducation,
  hasFinishedMostRecentEducation,
  isHighSchoolLevelEducation,
  hasAnyExperience,
  hasAnyFluentLanguage,
  fluentLanguages,
  (hasAnyEducation,
    hasFinishedMostRecentEducation,
    isHighSchoolLevelEducation,
    hasAnyExperience,
    hasAnyFluentLanguage,
    fluentLanguages) => {
    switch (true) {
      case hasAnyExperience && !hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` I am fluent in ${fluentLanguages}`
      case !hasAnyExperience && !hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` I am fluent in ${fluentLanguages}.`
      case hasAnyEducation && hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` and I am fluent in ${fluentLanguages}.`
      case !hasAnyEducation && hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` I am fluent in ${fluentLanguages}.`
      case hasAnyExperience && !hasFinishedMostRecentEducation && isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` I am fluent in ${fluentLanguages}`
      case !hasAnyExperience && !hasFinishedMostRecentEducation && isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` I am fluent in ${fluentLanguages}.`
      default:
        return ''
    }
  }
)

export const descriptionExperience = createSelector(
  hasAnyFluentLanguage,
  hasAnyExperience,
  hasAnyPreferences,
  hasFinishedMostRecentEducation,
  isHighSchoolLevelEducation,
  experiencePosition,
  experienceOrganisationName,
  experienceIsCurrent,
  (hasAnyFluentLanguage,
    hasAnyExperience,
    hasAnyPreferences,
    hasFinishedMostRecentEducation,
    isHighSchoolLevelEducation,
    experiencePosition,
    experienceOrganisationName,
    experienceIsCurrent) => {
    const handlePastExperience = experienceIsCurrent ? `currently work` : `have previous experience`

    switch (true) {
      case hasAnyExperience && !hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` and ${handlePastExperience} as ${withBeforeNoun(experiencePosition)} at ${experienceOrganisationName}.`
      case hasAnyExperience && !hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && !hasAnyFluentLanguage:
        return ` I ${handlePastExperience} as ${withBeforeNoun(experiencePosition)} at ${experienceOrganisationName}.`
      case hasAnyExperience && hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && hasAnyPreferences:
        return ` With ${experienceIsCurrent ? `current work` : `previous experience`} as ${withBeforeNoun(experiencePosition)} at ${experienceOrganisationName},`
      case hasAnyExperience && hasFinishedMostRecentEducation && !isHighSchoolLevelEducation && !hasAnyPreferences:
        return ` I ${handlePastExperience} as ${withBeforeNoun(experiencePosition)} at ${experienceOrganisationName}.`
      case hasAnyExperience && !hasFinishedMostRecentEducation && isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` and ${handlePastExperience} as ${withBeforeNoun(experiencePosition)} at ${experienceOrganisationName}.`
      case hasAnyExperience && !hasFinishedMostRecentEducation && isHighSchoolLevelEducation && !hasAnyFluentLanguage:
        return ` I ${handlePastExperience} as ${withBeforeNoun(experiencePosition)} at ${experienceOrganisationName}.`
      case hasAnyExperience && hasFinishedMostRecentEducation && isHighSchoolLevelEducation && hasAnyFluentLanguage:
        return ` and ${handlePastExperience} as ${withBeforeNoun(experiencePosition)} at ${experienceOrganisationName}.`
      case hasAnyExperience && hasFinishedMostRecentEducation && isHighSchoolLevelEducation && !hasAnyFluentLanguage:
        return ` I ${handlePastExperience} as ${withBeforeNoun(experiencePosition)} at ${experienceOrganisationName}.`
      default:
        return ''
    }
  }
)

const descriptionSummary = ' If you would like to learn more, please don’t hesitate to reach out on Oliv.'

export const aboutMeAutoDescription = createSelector(
  hasFinishedMostRecentEducation,
  isHighSchoolLevelEducation,
  descriptionFullName,
  descriptionEducation,
  descriptionPreferences,
  descriptionLanguage,
  descriptionExperience,
  (hasFinishedMostRecentEducation,
    isHighSchoolLevelEducation,
    descriptionFullName,
    descriptionEducation,
    descriptionPreferences,
    descriptionLanguage,
    descriptionExperience) => {
    switch (true) {
      case R.all(R.isEmpty)([descriptionEducation, descriptionPreferences, descriptionLanguage, descriptionExperience]):
        return null
      case !hasFinishedMostRecentEducation && !isHighSchoolLevelEducation:
      case !hasFinishedMostRecentEducation && isHighSchoolLevelEducation:
      case hasFinishedMostRecentEducation && isHighSchoolLevelEducation:
        return (
          `${descriptionFullName}<br /><br />${descriptionEducation}${descriptionPreferences}${descriptionLanguage}${descriptionExperience}<br /><br />${descriptionSummary}`
        )
      case hasFinishedMostRecentEducation && !isHighSchoolLevelEducation:
        return (
          `${descriptionFullName}<br /><br />${descriptionExperience}${descriptionPreferences}${descriptionEducation}${descriptionLanguage}<br /><br />${descriptionSummary}`
        )
      default:
        return null
    }
  }
)



